import React, {Fragment} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import DocumentTitle from 'react-document-title'
import {getMenu} from '../constant/menu'
import {urlToList} from '../utils/utils'

import GlobalHeader from '../components/GlobalHeader'
import SiderMenu from '../components/SiderMenu'

/**
 * 获取面包屑映射
 * @param {Object} menuData 菜单配置
 * @param {Object} routerData 路由配置
 */
const getBreadcrumbNameMap = (menuData, routerData) => {
  const result = {};
  const childResult = {};
  for (const i of menuData) {
    if (!routerData[i.path]) {
      result[i.path] = i;
    }
    if (i.children) {
      Object.assign(childResult, getBreadcrumbNameMap(i.children, routerData));
    }
  }
  return Object.assign({}, routerData, result, childResult);
};

class BasicLayout extends React.PureComponent {
  static childContextTypes = {
    location: PropTypes.object,
    breadcrumbNameMap: PropTypes.object,
  };

  constructor(props){
    super(props)
    this.state = {
      getMenuData: [],
      redirectData: [],
    }
    this.handleMenuCollapse = this.handleMenuCollapse.bind(this)
  }

  getChildContext() {
    const { location, routerData } = this.props;
    return {
      location,
      // breadcrumbNameMap: getBreadcrumbNameMap(this.state.getMenuData, routerData),
    };
  }

  componentDidMount() {
    // 获取用户信息
    /*this.props.dispatch({
      type: 'user/fetchCurrent',
    });*/

    this.initRedirect();
  }

  initRedirect() {
    // 判断侧边栏数据类型scholl or base
    const getMenuData = getMenu()

    /**
     * 根据菜单取得重定向地址.
     */
    const redirectData = [];
    const getRedirect = item => {
      if (item && item.children) {
        if (item.children[0] && item.children[0].path) {
          redirectData.push({
            from: `${item.path}`,
            to: `${item.children[0].path}`,
          });
          item.children.forEach(children => {
            getRedirect(children);
          });
        }
      }
    };

    getMenuData.forEach(getRedirect);
    this.setState({
      redirectData,
      getMenuData,
    });
  }

  getPageTitle() {
    const { location } = this.props;
    const { pathname } = location;
    const { getMenuData } = this.state;

    let curData = getMenuData.length && this.getMenuName(getMenuData, pathname)
    return curData ? `${curData.name} --寻踪智能缺陷检测` : '寻踪智能缺陷检测'
  }

  getMenuName(menuData, pathname){
    let paths = urlToList(pathname);
    let title = '', curObj = menuData

    paths.forEach(item => {
      curObj = curObj.filter( data => data.path == item)
      if ( curObj[0] && curObj[0].children ){
        curObj = curObj[0].children
      }
    })
    return curObj[0]
  }


  getBashRedirect = () => {
    // According to the url parameter to redirect
    // 这里是重定向的,重定向到 url 的 redirect 参数所示地址
    const urlParams = new URL(window.location.href);

    const redirect = urlParams.searchParams.get('redirect');

    // Remove the parameters in the url
    if (redirect) {
      urlParams.searchParams.delete('redirect');
      window.history.replaceState(null, 'redirect', urlParams.href);
    } else {
      const { routerData } = this.props;
      // get the first authorized route path in routerData
      const authorizedPath = Object.keys(routerData).find(
        item => check(routerData[item].authority, item) && item !== '/'
      );
      return authorizedPath;
    }
    return redirect;
  };

  // sdebar 是否收缩
  handleMenuCollapse(collapsed){
    this.props.dispatch({
      type: 'global/changeLayoutCollapsed',
      payload: collapsed,
    });
  }

  render() {
    const {
      currentUser,
      collapsed,
      fetchingNotices,
      notices,
      match,
      location,
      children
    } = this.props;

    const { getMenuData } = this.state;

    const layout = (
      <React.Fragment>
      <header>
        <GlobalHeader
        />
      </header>
        <div style={{ left: collapsed ? '-201px': 0}} className="container">
        <SiderMenu
          menuData={getMenuData}
          collapsed={collapsed}
          location={location}
          onCollapse={this.handleMenuCollapse}
        />
        {children}
      </div>
      </React.Fragment>
    );

    return (
      <DocumentTitle title={this.getPageTitle()}>
        {layout}
      </DocumentTitle>
    );
  }
}

export default connect(({ user, global, loading }) => ({
  currentUser: '',
  collapsed: global.collapsed,
  fetchingNotices: loading.effects['global/fetchNotices'],
  notices: 121212 || global.notices,
}))(BasicLayout);
