import {mockImage} from './state'

import {confirm, Dialog, TopTips} from '@tencent/bere'
import rejudgeCfg from '@config/rejudge'
import utils from '@modules/rejudge_v3/utils'
import axios from '@modules/rejudge_v3/lib/axios.min'

let {codeId, MODEL, formDataHeader,testImageUrl} = rejudgeCfg

let timer

let TopTipsduration = {
  err: 3000
}

// 上传文件路径集合
let UploadUrlMap = {
  sampleSys: '/api/v1/judge/sample/upload?sfpp_id=0',
  rejudgeSys: '/api/v1/judge/duplicate/upload?sfpp_id=0',
  spotCheckSys: '/api/v1/judge/extract/upload?sfpp_id=0',
}
// 启动接口路径集合
let start_UrlMap = {
  sampleSys: '',
  rejudgeSys: '',
  spotCheckSys: '',
}
// 获取图片接口路径集合
let QueryUrlMap = {
  sampleSys: '',
  rejudgeSys: '',
  spotCheckSys: '',
}

//历史记录路径集合
let logUrlMap = {
  sampleSys: '',
  rejudgeSys: '',
  spotCheckSys: '',
}
//更新图片信息路径集合
let UpdateImageUrlMap = {
  sampleSys: '',
  rejudgeSys: '',
  spotCheckSys: '',
}
// 上传文件路径集合
let task_UrlMap = {
  sampleSys: '',
  rejudgeSys: '',
  spotCheckSys: '',
}

export default {
  * startTask(action, {select, put, call, context}) {
    yield put({type: 'CHANGE_BASE_STATE', ...action.data})
  },
  * initialize(action, {select, put, call, context}) {
    const {sysActive, rejudgeType} = yield select(state => state[MODEL])
    yield put({type: 'Initialize', ...action.data})
  },
  * changBaseState(action, {select, put, call, context}) {
    yield put({type: 'CHANGE_BASE_STATE', ...action.data})
    if (action.callback) {
      action.callback()
    }
  },
  // 页面初始化查询一次有没有历史记录
  * sampleUploadLog(action, {select, put, call, context}) {
    const {sysActive, rejudgeType} = yield select(state => state[MODEL])
    start_UrlMap = {
      sampleSys: '',
      rejudgeSys: context.service.rejudge_start,
      spotCheckSys: context.service.extract_start,
    }
    QueryUrlMap = {
      sampleSys: context.service.sampleImg,
      rejudgeSys: context.service.rejudge_getImage,
      spotCheckSys: context.service.extract_getImg,
    }
    UpdateImageUrlMap = {
      sampleSys: context.service.updateSampleImg,
      rejudgeSys: context.service.updateImgDuplicate,
      spotCheckSys: context.service.UpdateImgExtract,
    }
    task_UrlMap = {
      sampleSys: context.service.sample_task,
      rejudgeSys: context.service.duplicate_task,
      spotCheckSys: context.service.extract_task,
    }
    logUrlMap = {
      sampleSys: context.service.sampleLog,
      rejudgeSys: context.service.duplicateLog,
      spotCheckSys: context.service.extractLog,
    }
    let params = {}
    params.q = JSON.stringify({
      filters:
        [
          {name: 'op', op: 'eq', val: localStorage.user_name},
          {name: 'status', op: 'lt', val: 1}
        ]
    })
    params.timer = new Date().getTime()

    try {
      const {resp} = yield call(logUrlMap[sysActive], params)
      // resp.num_results如果为0，那就是没有历史记录，就不用去查询code列表了
      if (resp.code === 0) {

        if (resp.num_results) {
          let _list = resp.data
          _list = _list[_list.length - 1] // 拿最后一条，也就是最新的一条

          /** 需要获取的数据是 task_id = id， id = last_detail_id sfpp_id = sfpp_id
           * 三个id为查询依据
           * **/
          let {sample_name, id, last_detail_id, sfpp_id, last_page} = _list
          yield put({
            type: 'CHANGE_BASE_STATE',
            upload_sample_name: sample_name,
            task_id: id,
            id: last_detail_id,
            sfpp_id: sfpp_id,
            // imageCurrentIndex: last_page ? last_page - 1 : 0,
            imageCurrentIndex: last_page ? last_page - 1 : 0,
          })
          TopTips({
            theme: 'success',
            message: '存在历史记录！'
          })
          action.callback({result: true, resp})
        } else {
          action.callback({result: false})
        }

      } else {
        action.callback({result: false})
      }
    } catch (err) {
      TopTips({
        theme: 'error',
        message: '获取历史记录的时候出现异常。',
        duration: TopTipsduration.err
      })
    }

  },
  // 样本验证查询条件code列表数据
  * sampleCodeList(action, {select, put, call, context}) {
    let params = {
      page: 1, // 第几页
      results_per_page: 100, // 一页有多少项
    }

    params.q = JSON.stringify(
      {
        filters: handleFlaskQ({op: localStorage.user_name})
      }
    )
    params.timer = new Date().getTime()

    const {resp} = yield call(context.service.getCodeList, params)
    if (resp.code === 0) {
      let sampleCodeList = resp.data[0].map((item, index) => {
        return {
          text: item.code,
          value: item.code,
        }
      })
      sampleCodeList.unshift({text: '全部', value: 0})
      yield put({type: 'CHANGE_BASE_STATE', sampleCodeList})

    }
  },
  /**
   * 启动复判
   * */ * rejudge_start(action, {select, put, call, context}) {
    const {sysActive, rejudgeType, sfpp_id} = yield select(state => state[MODEL])
    let {queryMap} = action

    let params = {}
    params.sfpp_id = sfpp_id ? sfpp_id : 0
    params.q = JSON.stringify({filters: handleFlaskQ(queryMap)})
    params.timer = new Date().getTime()

    yield put({type: 'CHANGE_BASE_STATE', startTask: true, getImageState: true})
    try{
      const {resp} = yield call(start_UrlMap[sysActive], params)

      yield put({type: 'CHANGE_BASE_STATE', getImageState: false})
      if (resp.code === 0) {
        let _data = resp.data

        /** 需要获取的数据是
         *  task_id = id，
         *  id = last_detail_id
         *  sfpp_id = sfpp_id 三个id为查询依据**/
        yield put({
          type: 'CHANGE_BASE_STATE',
          task_id: _data.task_id,
        })
        action.callback({result: true, resp})
      } else {
        action.callback({result: false, resp})
        utils.end_getImageLoad()
        yield put({type: 'CHANGE_BASE_STATE', startTask: false})
        TopTips({theme: 'error', message: resp.msg, duration: TopTipsduration.err})
      }
    }catch (err){
      yield put({type: 'CHANGE_BASE_STATE', startTask: false, getImageState: false})
      TopTips({theme: 'error', message: '启动任务的时候发生了异常情况。', duration: TopTipsduration.err})
      console.error(err)
    }

  },
  /**
   * 启动抽检
   * */
  * extract_start(action, {select, put, call, context}) {
    const {sysActive, rejudgeType, sfpp_id} = yield select(state => state[MODEL])
    let {queryMap, ratio} = action

    let params = {}
    params.sfpp_id = sfpp_id

    // 按文件抽检，没有比例
    if (ratio) {
      params.ratio = ratio
    }
    params.q = JSON.stringify({filters: handleFlaskQ(queryMap)})
    params.timer = new Date().getTime()

    yield put({type: 'CHANGE_BASE_STATE', startTask: true, getImageState: true})
    try{
      const {resp} = yield call(start_UrlMap[sysActive], params)

      yield put({type: 'CHANGE_BASE_STATE', getImageState: false})
      if (resp.code === 0) {
        let _data = resp.data

        yield put({
          type: 'CHANGE_BASE_STATE',
          task_id: _data.task_id,
        })
        action.callback({result: true, resp})
      } else {
        utils.end_getImageLoad()
        yield put({type: 'CHANGE_BASE_STATE', startTask: false})
        action.callback({result: false, resp})
        TopTips({theme: 'error', message: resp.msg, duration: TopTipsduration.err})
      }
    }catch (err) {
      yield put({type: 'CHANGE_BASE_STATE', startTask: false, getImageState: false})
      TopTips({theme: 'error', message: '启动任务的时候发生了异常情况。', duration: TopTipsduration.err})
      console.error(err)
    }

  },
  /**
   * 非按产品复判的时候，使用此获取图片方法
   * 执行查询，需要重新获取图片信息、当前图片位置，当前图片总数
   * */
  * handleQueryImage(action, {select, put, call, context}) {
    let params
    const model = yield select(state => state[MODEL])
    let {imageCurrentIndex, task_id, sfpp_id, sysActive, rejudgeType, saveQueryCondition,imageCache,startLoadImage} = model

    // 如果保存的搜索条件存在，则是快捷键切换图片在调用本函数，否则调用获取图片按钮的搜索条件
    let queryMap = saveQueryCondition ? saveQueryCondition : {...action.queryMap}

    // 如果id存在，表示存在历史记录，但是查询不带其他参数，只带id,op,task_id 234324324324
    queryMap.task_id = task_id
    sfpp_id = sfpp_id ? sfpp_id : 0// 四元组id

    params = {
      page: imageCurrentIndex + 1, // 第几页
      results_per_page: 1, // 一页有多少项
      sfpp_id,
    }
    // 抽检系统的抽检比例
    // if (action.ration) {
    //   params.ration = action.ration
    // }

    /* 添加q flask框架的查询语句 */
    params.q = JSON.stringify({filters: handleFlaskQ(queryMap)})
    params.timer = new Date().getTime()

    utils.start_checkImageExist()

    yield put({type: 'CHANGE_BASE_STATE', startTask: true, getImageState: true})
    try{

      //魔术来了
      // const res = yield call(QueryUrlMap[sysActive], params)
      let _resData;
      let _cacheImageMap;
      let _showData;
      let afterResp;
      let _page;
      let _imageCurrentIndex = imageCurrentIndex + 1
      let _beforeRadius = 10;
      let _startLoadIndex = 1;
      let _loadIndex = 0;
      let afterParams = {
        results_per_page: _beforeRadius,
        sfpp_id: sfpp_id
      }

      // console.log('_imageCurrentIndex', _imageCurrentIndex)
      if (_imageCurrentIndex > _beforeRadius) {
        _page = Math.ceil(_imageCurrentIndex / _beforeRadius)
        afterParams.page = _page
        _loadIndex = _startLoadIndex * (_page - 1) * 10 + 1
      } else {
        _page = Math.floor(_imageCurrentIndex / _beforeRadius)
        afterParams.page = 1
        _loadIndex = 1
      }
      // console.log('页数', _page)

      // afterParams.page = _page > 1 ? _page : 1
      // _loadIndex = afterParams.page >= 2 ? _startLoadIndex * afterParams.page * _beforeRadius : 1

      // console.log('开始下标_loadIndex', _loadIndex)

      afterParams.q = JSON.stringify({filters: handleFlaskQ(queryMap)})
      afterParams.timer = new Date().getTime()
      /* 如果缓存里的数据长度为0，就开始请求新的数据 */
      if (startLoadImage || !imageCache || !imageCache.length) {

        yield put({type: 'CHANGE_BASE_STATE', imageCache: [], imageCacheRange: []})
        // console.log('afterParams请求参数', afterParams)

        afterResp = yield call(QueryUrlMap[sysActive], afterParams)
        let afterRespRes = afterResp.resp

        // console.log('afterRespRes返回结果', afterRespRes)
        if (afterRespRes.code !== 0) {
          TopTips({theme: 'error', message: afterRespRes.msg, duration: TopTipsduration.err})
          yield put({type: 'CHANGE_BASE_STATE', startTask: false, startLoadImage: true})
          action.callback({result: false})
          return
        }

        let _num_results = afterRespRes.num_results

        _cacheImageMap = new Array(_num_results)
        yield put({type: 'CHANGE_BASE_STATE', num_results: _num_results, startLoadImage: false})
        // console.log('_cacheImageMap', _cacheImageMap)

        _resData = afterRespRes.data[0]
        for (let i = 0; i < _resData.length; i++) {
          /** 把请求到的数据，添加到new Image里加载，然后加载到imageCache里存储 */
          let img = new Image()
          img.src = testImageUrl + _resData[i].img_url

          _cacheImageMap[_loadIndex + i] = _resData[i]
        }

        // console.log('_cacheImageMap', _cacheImageMap)
        yield put({
          type: 'CHANGE_BASE_STATE',
          imageCache: _cacheImageMap,
          imageCacheRange: [_loadIndex, _loadIndex + _resData.length - 1]
        })
        let {imageCache, imageCacheRange} = yield select(state => state[MODEL])
        // console.log('需要加载新的数据的缓存', _imageCurrentIndex, imageCache, imageCacheRange)
        _showData = imageCache[_imageCurrentIndex]
        /** 如果当前查看的图片接近缓存临界点，那么就清空图片缓存，准备下一次加载新的数据 */
        if (_imageCurrentIndex === imageCacheRange[0] || _imageCurrentIndex === imageCacheRange[1]) {
          yield put({
            type: 'CHANGE_BASE_STATE',
            startLoadImage: true
          })
          // console.log('准备加载新的数据！！！！！！！！！！！！！')
        }
      } else {

        let {imageCache, imageCacheRange} = yield select(state => state[MODEL])
        // console.log('加载数据的缓存', imageCache, imageCacheRange)
        _showData = imageCache[_imageCurrentIndex]
        /** 如果当前查看的图片接近缓存临界点，那么就清空图片缓存，准备下一次加载新的数据 */
        if (_imageCurrentIndex === imageCacheRange[0] || _imageCurrentIndex === imageCacheRange[1]) {
          yield put({
            type: 'CHANGE_BASE_STATE',
            startLoadImage: true
          })
          // console.log('准备加载新的数据！！！！！！！！！！！！！')
        }
      }
      /** 取新的imageCache */
      // console.log('需要展示的当前图片', _showData)
      let {num_results} = yield select(state => state[MODEL])
      let resp = {
        code: 0,
        data: [[_showData]],
        num_results: num_results
      }

      // // console.log('单张获取的参数', params)
      let on_res = yield call(QueryUrlMap[sysActive], params)

      utils.end_getImageLoad()

      // let resp={}
      // const res = yield call(QueryUrlMap[sysActive], params)
      // resp = res.resp
      // yield put({type: 'CHANGE_BASE_STATE', getImageState: false})
      // utils.end_getImageLoad()
      //
      // let _resp = resp
      if (resp.code === 0) {
        if (!resp.data[0].length) {
          confirmModal('获取图片', `没有图片信息，请上传文件`, 'warning')
          return action.callback({result: false})
        }

        action.callback({
          result: true,
          _resp: resp,
          queryMap: queryMap,
          params: params
        })

      } else {
        yield put({type: 'CHANGE_BASE_STATE', startTask: false})
        action.callback({result: false})

        TopTips({
          theme: 'error',
          message: resp.msg,
          duration: TopTipsduration.err
        })

      }
    }catch (err) {
      yield put({type: 'CHANGE_BASE_STATE', startTask: false, getImageState: false})
      TopTips({theme: 'error', message: '获取图片的时候，发生了异常情况。', duration: TopTipsduration.err})
      console.error(err)
    }
  },
  /**
   * 获取图片
   * */
  * rejudge_getImage(action, {select, put, call, context}) {
    //let { queryMap } = action;
    let {sysActive,
      imageCurrentIndex,
      task_id,
      sfpp_id,
      imageCache,
      startLoadImage,
      } = yield select(state => state[MODEL])
    let queryMap = {}
    queryMap.task_id = task_id
    /**查询条件有，sfpp_id，q里的task_id* */

    let params = {
      page: imageCurrentIndex + 1, // 第几页
      results_per_page: 1, // 一页有多少项
      sfpp_id: sfpp_id
    }
    /* 添加q flask框架的查询语句 */
    params.q = JSON.stringify({filters: handleFlaskQ(queryMap)})
    params.timer = new Date().getTime()

    utils.start_checkImageExist()
    yield put({type: 'CHANGE_BASE_STATE', startTask: true, getImageState: true})
    try{
      //魔术来了
      let _resData;
      let _cacheImageMap;
      let _showData;
      let afterResp;
      let _page;
      let _imageCurrentIndex = imageCurrentIndex + 1
      let _beforeRadius = 10;
      let _startLoadIndex = 1;
      let _loadIndex = 0;
      let afterParams = {
        results_per_page: _beforeRadius,
        sfpp_id: sfpp_id
      }

      // console.log('_imageCurrentIndex', _imageCurrentIndex)
      if (_imageCurrentIndex > _beforeRadius) {
        _page = Math.ceil(_imageCurrentIndex / _beforeRadius)
        afterParams.page = _page
        _loadIndex = _startLoadIndex * (_page - 1) * _beforeRadius + 1
      } else {
        _page = Math.floor(_imageCurrentIndex / _beforeRadius)
        afterParams.page = 1
        _loadIndex = 1
      }
      // console.log('页数', _page)

      // afterParams.page = _page > 1 ? _page : 1
      // _loadIndex = afterParams.page >= 2 ? _startLoadIndex * afterParams.page * _beforeRadius : 1

      // console.log('开始下标_loadIndex', _loadIndex)

      afterParams.q = JSON.stringify({filters: handleFlaskQ(queryMap)})
      afterParams.timer = new Date().getTime()
      /* 如果缓存里的数据长度为0，就开始请求新的数据 */
      if (startLoadImage || !imageCache || !imageCache.length) {

        yield put({type: 'CHANGE_BASE_STATE', imageCache: [], imageCacheRange: []})
        // console.log('afterParams请求参数', afterParams)

        afterResp = yield call(QueryUrlMap[sysActive], afterParams)
        let afterRespRes = afterResp.resp

        // console.log('afterRespRes返回结果', afterRespRes)
        if (afterRespRes.code !== 0) {
          TopTips({theme: 'error', message: afterRespRes.msg, duration: TopTipsduration.err})
          yield put({type: 'CHANGE_BASE_STATE', startTask: false, startLoadImage: true})
          action.callback({result: false})
          return
        }

        let _num_results = afterRespRes.num_results

        _cacheImageMap = new Array(_num_results)
        yield put({type: 'CHANGE_BASE_STATE', num_results: _num_results, startLoadImage: false})
        // console.log('_cacheImageMap', _cacheImageMap)

        _resData = afterRespRes.data[0]
        for (let i = 0; i < _resData.length; i++) {
          /** 把请求到的数据，添加到new Image里加载，然后加载到imageCache里存储 */
          let img = new Image()
          img.src = testImageUrl + _resData[i].img_url

          _cacheImageMap[_loadIndex + i] = _resData[i]
        }

        // console.log('_cacheImageMap', _cacheImageMap)
        yield put({
          type: 'CHANGE_BASE_STATE',
          imageCache: _cacheImageMap,
          imageCacheRange: [_loadIndex, _loadIndex + _resData.length - 1]
        })
        let {imageCache, imageCacheRange} = yield select(state => state[MODEL])
        // console.log('需要加载新的数据的缓存', _imageCurrentIndex, imageCache, imageCacheRange)
        _showData = imageCache[_imageCurrentIndex]
        /** 如果当前查看的图片接近缓存临界点，那么就清空图片缓存，准备下一次加载新的数据 */
        if (_imageCurrentIndex === imageCacheRange[0] || _imageCurrentIndex === imageCacheRange[1]) {
          yield put({
            type: 'CHANGE_BASE_STATE',
            startLoadImage: true
          })
          // console.log('准备加载新的数据！！！！！！！！！！！！！')
        }
      } else {

        let {imageCache, imageCacheRange} = yield select(state => state[MODEL])
        // console.log('加载数据的缓存', imageCache, imageCacheRange)
        _showData = imageCache[_imageCurrentIndex]
        /** 如果当前查看的图片接近缓存临界点，那么就清空图片缓存，准备下一次加载新的数据 */
        if (_imageCurrentIndex === imageCacheRange[0] || _imageCurrentIndex === imageCacheRange[1]) {
          yield put({
            type: 'CHANGE_BASE_STATE',
            startLoadImage: true
          })
          // console.log('准备加载新的数据！！！！！！！！！！！！！')
        }
      }
      /** 取新的imageCache */
      // console.log('需要展示的当前图片', _showData)
      let {num_results} = yield select(state => state[MODEL])
      let resp = {
        code: 0,
        data: [[_showData]],
        num_results: num_results
      }

      // // console.log('单张获取的参数', params)
      let on_res = yield call(QueryUrlMap[sysActive], params)

      utils.end_getImageLoad()

      yield put({type: 'CHANGE_BASE_STATE', getImageState: false})
      if (resp.code === 0) {
        /** 成功获取图片并开启任务 */
        action.callback({result: true, _resp: resp, queryMap: queryMap, params: params})

      } else {
        yield put({type: 'CHANGE_BASE_STATE', startTask: false})
        action.callback({result: false})
        TopTips({theme: 'error', message: resp.msg, duration: TopTipsduration.err})
      }
    }catch (err) {
      yield put({type: 'CHANGE_BASE_STATE', startTask: false, getImageState: false})
      TopTips({theme: 'error', message: '获取图片的时候，发生了异常情况。', duration: TopTipsduration.err})
      console.error(err)
    }

  },
  // 获取图片成功之后，配置到图片信息里
  * setImageInfo(action, {select, put, call, context}) {
    // // console.log('获取图片成功之后，配置到图片信息里', action)
    let {_resp, queryMap} = action
    TopTips({
      theme: 'success',
      message: '获取图片成功！'
    })

    let _imageInfos
    _imageInfos = _resp.data[0]

    /**开始复判，这个值是为了防止在没有获取到图片的时候就可以使用快捷键 todo*/
    yield put({type: 'CHANGE_BASE_STATE', startTask: true, getImageState: false})

    yield put({
      type: 'CHANGE_BASE_STATE',
      num_results: _resp.num_results, // 显示总页数
      saveQueryCondition: {...queryMap}  // 保存搜索条件，供快捷键调用
    })

    // 添加图片
    if (_imageInfos.length) {
      yield put({
        type: 'CHANGE_BASE_STATE',
        imageInfo: _imageInfos[0],
        codeBtnActiveNumber: _imageInfos[0].valid_code
      })
      utils.end_checkImageExist('', _imageInfos[0].img_url)

    }
  },

  /**
   * 切换下一张图片
   * 切换图片不作请求，只改变页数
   * */
  * nextImg(action, {select, put, call, context}) {
    // // console.log('action', action)
    const {imageCurrentIndex, num_results} = yield select(state => state[MODEL])
    if (imageCurrentIndex + 1 === num_results) return

    let num
    num = imageCurrentIndex + 1
    yield put({
      type: 'CHANGE_BASE_STATE',
      imageCurrentIndex: num,
      codeBtnActiveNumber: ''
    })
    action.callback(num)
  },

  /**
   * 切换上一张图片
   * 切换图片不作请求，只改变页数
   * */
  * previousImg(action, {select, put, call, context}) {
    // // console.log('action', action)
    const {imageCurrentIndex} = yield select(state => state[MODEL])
    if (!imageCurrentIndex) return

    let num
    num = imageCurrentIndex - 1
    yield put({
      type: 'CHANGE_BASE_STATE',
      imageCurrentIndex: num,
      codeBtnActiveNumber: ''
    })
    action.callback(num)
  },
  /**
   * 上传文件/图片
   * */
  * requestSampleUpload(action, {select, put, call, context}) {
    // document.cookie = "op=123"
    const file = action.file
    const {sysActive, rejudgeType, uploadState} = yield select(state => state[MODEL])
    let _suffix
    if (rejudgeType === 'file') {
      _suffix = 'csv'
    } else {
      _suffix = 'zip'
    }

    if (!utils.checkUploadFileSuffix(file.name, _suffix)) {
      TopTips({
        theme: 'error',
        message: `上传的文件类型不合规`,
        duration: TopTipsduration.err
      })
      return
    }

    if (!utils.checkUploadFileSize(file, 1024 * 1024 * 1024)) {
      TopTips({
        theme: 'error',
        message: `上传的文件超过 1G 大小限制`,
        duration: TopTipsduration.err
      })
      return
    }
    // // console.log(utils.checkUploadFileType(action.file.name))

    let formData = new FormData()
    formData.append('file', action.file)
    yield put({type: 'CHANGE_BASE_STATE', uploadState: true})

    let res
    try {
      res = yield axios.post(UploadUrlMap[sysActive], formData, formDataHeader)
      yield put({type: 'CHANGE_BASE_STATE', uploadState: false})

      if (res.data.code === 0) {
        TopTips({
          theme: 'success',
          message: '上传文件成功!'
        })
        let _data = res.data.data

        if (!_data.codeList || !_data.codeList.length) {
          TopTips({
            theme: 'error',
            message: '没有code信息!',
            duration: TopTipsduration.err
          })
          /*     confirmModal('获取code列表', `没有code信息，请检查上传文件`, 'warning')*/
          return false
        }
        let sampleCodeList = _data.codeList.map((item, index) => {
          return {
            text: item.code,
            value: item.code,
          }
        })
        sampleCodeList.unshift({text: '全部', value: 0})
        let {task_id, upload_sample_name} = _data

        yield put({
          type: 'CHANGE_BASE_STATE',
          sampleCodeList,
          task_id: task_id,
          upload_sample_name: upload_sample_name
        })
      }
      else {
        TopTips({
          theme: 'error',
          message: res.data.msg,
          duration: TopTipsduration.err
        })
      }
    } catch (err) {
      yield put({type: 'CHANGE_BASE_STATE', uploadState: false})
      TopTips({
        theme: 'error',
        message: '上传文件出现异常，请重新尝试。',
        duration: TopTipsduration.err
      })
    }

  },
  /**
   * 图片详情右侧导入Code
   * */
  * imageImportCode(action, {select, put, call, context}) {
    let {file} = action
    if (!utils.checkUploadFileSuffix(file.name, 'csv')) {
      TopTips({
        theme: 'error',
        message: `上传的文件类型不合规`,
        duration: TopTipsduration.err
      })
      return
    }
    if (!utils.checkUploadFileSize(file, 1024 * 1024 * 5)) {
      TopTips({theme: 'error', message: `上传的文件超过 5M 大小限制`, duration: TopTipsduration.err})
      return
    }
    let formData = new FormData()
    formData.append('file', file)

    yield put({type: 'CHANGE_BASE_STATE', importCodeState: true})

    let res
    try {
      res = yield axios.post(`/api/v1/judge/code/upload?sfpp_id=0`, formData, formDataHeader)
      yield put({type: 'CHANGE_BASE_STATE', importCodeState: false})
      if (res.data.code === 0) {
        TopTips({theme: 'success', message: '导入code成功!'})
        action.callback({result: true, res: res})
      } else {
        action.callback({result: false, res: res})
        TopTips({theme: 'error', message: res.data.msg, duration: TopTipsduration.err})
      }
    } catch (e) {
      yield put({type: 'CHANGE_BASE_STATE', importCodeState: false})
      TopTips({
        theme: 'error',
        message: '导入code出现异常，请重新尝试。',
        duration: TopTipsduration.err
      })
    }

  },
  // 图片详情右侧文本框 获取code
  * queryCode(action, {select, put, call, context}) {
    let params = {
      page: 1, // 第几页
      results_per_page: 100, // 一页有多少项
    }

    params.q = JSON.stringify(
      {
        group_by:[{'field': 'code'}],
        order_by: [{'field': 'id'}],
        filters: handleFlaskQ({op: localStorage.user_name})
      }
    )
    params.timer = new Date().getTime()
    const res = yield call(context.service.getCodeList, params)
    let _resp = res.resp
    if (_resp.code === 0 && _resp.data[0].length) {
      let imageCodeList = _resp.data[0]
      yield put({type: 'CHANGE_BASE_STATE', imageCodeList})

      if (action.callback) {
        action.callback({
          result: true,
          data: {
            imageCodeList: imageCodeList
          }
        })
      }

    } else {
      action.callback({result: false,})

      TopTips({
        theme: 'error',
        message: _resp.msg,
        duration: TopTipsduration.err
      })
    }
  },

// 图片详情右侧文本框 添加code
  * addCode(action, {select, put, call, context}) {
    // document.cookie = 'op=123';
    let json = {
      code: action.data.code,
      sfpp_id: 0,
    }
    const res = yield call(context.service.codeAdd, json)
    let _resp = res.resp
    if (_resp.code === 0) {
      TopTips({
        theme: 'success',
        message: '增加code成功!'
      })

      action.callback({result: true, res: res})
    } else {
      action.callback({result: false, res: res})
      TopTips({
        theme: 'error',
        message: _resp.msg,
        duration: TopTipsduration.err
      })
    }

  },
  /**
   * 加载产品列表
   * */
  * getProductList(action, {select, put, call, context}) {
    // const { site, shop, operation } = yield select(state => state[MODEL].queryConditionMap.product)
    let {site, shop, operation} = action.data

    let params = {}
    params.q = JSON.stringify({
      filters: [
        {
          and: [
            {name: 'site', op: 'eq', val: site},
            {
              and: [
                {name: 'factory_name', op: 'eq', val: shop},
                {name: 'process_operation_name', op: 'eq', val: operation}
              ]
            }
          ]
        }
      ]
    })
    params.timer = new Date().getTime()
    const res = yield call(context.service.siteProduct, params)
    let _arr = []
    let _resp = res.resp
    if (_resp.code === 0) {
      for (let i = 0; i < _resp.data.length; i++) {
        _arr.push(
          {
            text: _resp.data[i].product_spec_name,
            value: _resp.data[i].id
          }
        )
      }
    }
    yield put({type: 'CHANGE_BASE_STATE', productList: _arr})

  },
  /**
   * 请求四元组的下拉框数据
   * */
  * requestSite(action, {select, put, call, context}) {
    // const { imageCurrentIndex, num_results, sysActive } = yield select(state => state[MODEL])
    let params = {}
    params.timer = new Date().getTime()
    const res = yield call(context.service.site, params)
    let _resp = res.resp
    if (_resp.code === 0) {
      yield put({type: 'CHANGE_BASE_STATE', siteMap: res.resp.data})
    } else {
      TopTips({
        theme: 'error',
        message: _resp.msg,
        duration: TopTipsduration.err
      })
    }

  },

  /**
   * 改变图片的三个按钮的状态
   * */
  * r_t_s(action, {select, put, call, context}) {
    const {sysActive,imageInfo, codeBtnActiveNumber, imageCache, imageCurrentIndex} = yield select(state => state[MODEL])
    let _imageCache = imageCache
    // 需要先选择code，才可以操作
    if (!codeBtnActiveNumber) {
      TopTips({theme: 'warning', message: '请先选择code。', duration: TopTipsduration.err})
      return
    }

    let _imageInfo = {...imageInfo, ...action.data}

    const {resp} = yield call(UpdateImageUrlMap[sysActive], _imageInfo)
    if (resp.code === 0) {
      let { turn_on, rework, sheet_type } = resp.data
      let _index = imageCurrentIndex + 1
      _imageCache[_index].turn_on = turn_on
      _imageCache[_index].rework = rework
      _imageCache[_index].sheet_type = sheet_type
      // console.log('改变前', _imageCache[_index])

      yield put({
        type: 'CHANGE_BASE_STATE',
        imageCache: _imageCache,
        imageInfo: _imageInfo,
        codeBtnActiveNumber: _imageCache[_index].valid_code
      })
      const {imageCache} = yield select(state => state[MODEL])
      // console.log('改变后', imageCache[_index])

      TopTips({
        theme: 'success',
        message: '操作成功!'
      })
    } else {
      TopTips({
        theme: 'error',
        message: resp.msg,
        duration: TopTipsduration.err
      })
    }
  },
  // * preselectionCode(action, {select, put, call}) {
  //
  // },
  * chooseCodeBtn(action, {select, put, call}) {
    yield put({type: 'CHOOSE_CODE_BTN', ...action.data})
    $(`#${codeId + action.data.valid_code}`).focus()

    let {sysActive, imageInfo, imageCache, imageCurrentIndex} = yield select(state => state[MODEL])
    // console.log(imageCache, imageCurrentIndex)
    let _imageCache = imageCache
    let _imageInfo = {...imageInfo, valid_code: action.data.valid_code}
    try {
      const { resp } = yield call(UpdateImageUrlMap[sysActive], _imageInfo)
      if (resp.code === 0) {
        let {valid_code } = resp.data
        let _index = imageCurrentIndex + 1
        _imageCache[_index].valid_code = valid_code


        yield put({
          type: 'CHANGE_BASE_STATE',
          imageCache: _imageCache,
          imageInfo: _imageCache[_index],
          codeBtnActiveNumber: _imageCache[_index].valid_code
        })

        TopTips({theme: 'success', message: '操作成功!'})
      } else {
        TopTips({theme: 'error', message: resp.msg, duration: TopTipsduration.err})
      }
    }catch (e) {
      TopTips({
        theme: 'error',
        message: '选择code的时候出现异常，请重新尝试。',
        duration: TopTipsduration.err
      })
      // console.log(e)
    }

  },
  // 切换系统
  * switchSystem(action, {select, put, call}) {
    yield put({type: 'CHANGE_BASE_STATE', ...action.data})
  },
  // 切换复判类型
  * switchRejudgeType(action, {select, put, call, context}) {
    yield put({type: 'CHANGE_BASE_STATE', ...action.data})
  },
  // 结束查看页面表格数据请求
  * getViewEndTableData(action, {put, select, call, context}) {
    let params = {
      page: action.current, // 第几页
      results_per_page: action.perPageCount, // 一页有多少项
    }

    params.q = JSON.stringify(
      {
        order_by: [
          {'field': 'create_time', 'direction': 'desc'}
        ],
        filters: [
          {name: 'op', op: 'eq', val: localStorage.user_name}
        ]
      }
    )
    params.timer = new Date().getTime()
    const {resp} = yield call(context.service.result_log, params)

    if (resp.code === 0) {
      action.callback({result: true, data: resp})
    } else {
      action.callback({result: false})
      TopTips({
        theme: 'error',
        message: resp.msg,
        duration: TopTipsduration.err
      })
    }
  },

  /**
   * 结束复判
   * */
  * endTask(action, {select, put, call, context}) {
    const {sysActive, rejudgeType, task_id} = yield select(state => state[MODEL])
    let obj = {
      sfpp_id: 0,
      status: 1,
      task_id
    }
    try {
      const res = yield call(task_UrlMap[sysActive], obj)
      let _resp = res.resp
      if (_resp.code === 0) {
        yield put({type: 'CHANGE_BASE_STATE', startTask: false})
        yield put({type: 'Initialize', sysActive, rejudgeType})

        action.callback({result: true})
      } else {
        action.callback({result: false})
        TopTips({
          theme: 'error',
          message: _resp.msg,
          duration: TopTipsduration.err
        })
      }
    } catch (err) {
      TopTips({
        theme: 'error',
        message: '结束出现异常，请重新尝试。',
        duration: TopTipsduration.err
      })
    }

  }
}

