
import moment from 'moment';
import { TopTips } from '@tencent/bere'
export default {
  namespace: 'statis',
  state: {
    serviceName:'Sum',
    // 厂区
    site: '',
    // 厂别
    shop: '',
    // 站点
    operation: '',
    // 产品
    product: '',
    // 时间类型
    timeType: 2,
    // 时间
    timeStart: '',
    // 时间
    timeEnd: '',
    filters:{},
    confidence_level:'',
    rule_version:'',
    algorithm_version:'',
    // 任务数据
    dataList: [],
    // 数据总数
    total: 0,
    // 每页数量
    limit: 15,
    // 当前页数
    page: 0,
    isLoading:false
  },
  reducers: {
  },
  effects: {
    * refresh(action, { select, put }) {
      const {
        site, shop, operation,product,timeType,timeStart,timeEnd,page, limit,serviceName,filters,confidence_level
      } = yield select(state => state.statis)

      yield put({
        type: 'statis/fetchDatas',
        payload: { site, shop, operation,product,timeType,timeStart,timeEnd,page, limit,serviceName,filters,confidence_level }
      })
    },

    * fetchDatas(action, { select, put, call, cancel, context }) {
      yield put({
        type: 'statis/change',
        payload: {
          dataList: [],
          isLoading: true
        }
      })
    
      const {
        algorithm_version, rule_version
      } = yield select(state => state.statis)

      let { site, shop, operation,product,timeType,timeStart,timeEnd,page, limit,serviceName,filters,confidence_level } = action.payload
      
      const params = {
        page: page + 1,
        results_per_page: limit
      }
      
      const q = {
        filters: []
      }
      // if (site && site!='-1') {
      //   q.filters.push({ name: 'site', op: 'eq', val: site })
      // }
      // if (shop && shop!='-1') {
      //   q.filters.push({ name: 'factory_name', op: 'eq', val: shop })
      // }
      // if (operation && operation!='-1') {
      //   q.filters.push({ name: 'process_operation_name', op: 'eq', val: operation })
      // }
      if (product && product!='-1') {
        //q.filters.push({ name: 'product_spec_name', op: 'eq', val: product })
        q['sfpp_id'] = product
      }
      else{//返回错误
        yield put({
          type: 'statis/change',
          payload: {
            dataList: [],
            total: 0,
            isLoading: false
          }
        })
        return;
      }
      if(confidence_level && serviceName=='Code'){
        q.filters.push({ name: 'confidence_level', op: 'ge', val: confidence_level })
      }
      if(algorithm_version){
        q.filters.push({ name: 'algorithm_version', op: 'eq', val: algorithm_version })
      }
      if(rule_version){
        q.filters.push({ name: 'rule_version', op: 'eq', val: rule_version })
      }

      if(serviceName=='Flagdiff'){
        q.filters.push({"or":[{ name: 'rtir_turn_on', op: 'eq', val: '1' },{ name: 'rtir_rework', op: 'eq', val: '1' }]})
      }
      else if(serviceName=='Flagleak'){
        q.filters.push({"or":[{ name: 'rtir_turn_on', op: 'eq', val: '1' },{ name: 'rtir_rework', op: 'eq', val: '1' }]})
        q.filters.push({ name: 'turn_on', op: 'eq', val: '' })
        q.filters.push({ name: 'rework', op: 'eq', val: 'N' })
      }
      else if(serviceName=='Diffdetail'){
        q.filters.push({ name: 'compare_rtir_code', op: 'eq', val: 2 })
      }

      if(filters){
        for(let k of Object.keys(filters)){
          if(filters[k]){
            q.filters.push({ name: k, op: 'eq', val: filters[k] })
          }
        }
      }
      if(timeType!=0){
        let endDate = new Date();
        var preDate;
        let pDays=0;
        let time_field = "handling_time"; 
        if(serviceName=='Sum'){
          time_field = "rtir_end_time"; 
        }
        switch (timeType) {
          case 1:
            pDays=0;
          break;
          case 2:
            pDays=1;
            endDate = new Date(endDate.getTime() - 24*60*60*1000); 
            preDate = endDate;
          break;
          case 3:
            pDays=7;
          break;
          case 4:
            pDays=30;
          break;
          default:
            break;
        }
        if(timeType!=-1){//固定日期
          preDate = preDate ||(new Date(endDate.getTime() - pDays*24*60*60*1000)); 
          timeStart = moment(preDate).format('YYYY-MM-DD');
          timeEnd = moment(endDate).format('YYYY-MM-DD');
        }
        if(timeStart){
          //前移1天
          timeStart = new Date(new Date(timeStart).getTime() - 24*60*60*1000); 
          timeStart = moment(timeStart).format('YYYY-MM-DD');
          console.log("timeStart")
          console.log(timeStart)
          q.filters.push({ name: time_field, op: 'gt', val: timeStart+" 07:30:00" })
        }
        if(timeEnd){
          console.log("timeEnd")
          console.log(timeEnd)
          q.filters.push({ name: time_field, op: 'le', val: timeEnd+" 07:30:00" })
        }
      }
      
      params.q = JSON.stringify(q)
      const { resp } = yield call(context.service["getStatis"+serviceName], params)
      
      if (resp.code==0) {
        yield put({
          type: 'statis/change',
          payload: {
            dataList: resp.data,
            total: resp.num_results,
            isLoading: false
          }
        })
      }
      else{
        yield put({
          type: 'statis/change',
          payload: {
            dataList: [],
            total: 0,
            isLoading: false
          }
        })
      }

    }
  }
}
