import _ from 'lodash/fp'
import { notification } from '@tencent/bere'
import fetch from "@utils/fetch.js"
import { filterNull } from '@utils/adapter'

export default {
  namespace: 'glass',
  state: {
    //是否显示添加页面
    addRulePageShow: false,
    //添加页面条件项和更新项
    addRulePageColum: null,
    //添加页面是否是编辑状态
    addRulePageIsEdit: false,
    //添加页面数据展示
    editColumData: null,
    //规则定义/规则录入tab选择
    navSelect: 'definition',
    //规则定义表格loading
    definitionTableLoading: false,
    //规则定义条件项数据
    conditionTableData: [],
    //规则定义更新项数据
    updateTableData: [],
    //规则录入表格loading
    ruleTableLoading: false,
    //规则录入表格数据
    ruleTableData: [],
    //规则录入表格配置
    ruleTableColum: [],
    //规则录入表格分页总数
    ruleTablePageTotal: 0,
    //规则录入表格当前页面
    ruleTablePageCurrent: 1,
    //规则录入表格每页条数
    ruleTablePageCount: 15,

    creatModalShow: {
      //显示创建框
      visible: false,
      //创建框是否是编辑
      isEdit: false,
      //创建框编辑数据时的初始数据
      initialValues: {
        ruleType: 'condition'
      }
    },
    uploadModalShow: {
      //显示上传框
      visible: false,
      //上传框初始数据
      initialValues: {
      }
    },
  },
  effects: {
    *fetchRuleDefinalTable({ payload }, { call, put, context: { service } }) {
      yield put({
        type: 'change',
        payload: {
          definitionTableLoading: true,
        }
      })
      let _conditionTableData = [];
      let _updateTableData = [];
      try {
        let { resp } = yield call(service.getRuleDefine, { type: 4 })
        if (resp.code !== 0) {
          notification.error('获取glass开单建议规则定义失败')
          throw resp.msg
        } else {
          _conditionTableData = resp.data.conditions;
          _updateTableData = resp.data.updates
        }
        yield put({
          type: 'change',
          payload: {
            conditionTableData: _conditionTableData,
            updateTableData: _updateTableData
          }
        })
        yield put({
          type: 'change',
          payload: {
            definitionTableLoading: false,
          }
        })
      } catch (error) {
        console.error("获取规则定义失败", error)
        // notification.error(typeof error === 'string' ? error : '获取规则定义失败')
      }
    },
    *fetchRuleTable({ payload }, { call, select, put, context: { service } }) {
      yield put({
        type: 'change',
        payload: {
          ruleTableLoading: true,
        }
      })
      let { glass: selfState } = yield select(_.identity)
      let _ruleTablePageTotal = 0,
        _ruleTablePageCurrent = selfState.ruleTablePageCurrent,
        _ruleTablePageCount = selfState.ruleTablePageCount,
        _ruleTableData = [],
        _ruleTableColum = [],
        conditionData = [],
        updateData = [];
      try {
        //先获取规则定义当表头
        if (!selfState.updateTableData.length > 0 && !selfState.conditionTableData > 0) {
          conditionData = selfState.updateTableData;
          updateData = selfState.conditionTableData;
        } else {
          let { resp: definRes } = yield call(service.getRuleDefine, { type: 4 })
          if (definRes.code !== 0) {
            notification.error('获取glass开单建议规则失败')
            throw definRes.msg
          }
          conditionData = definRes.data.conditions;
          updateData = definRes.data.updates;
        }

        //获取规则数据
        let { resp } = yield call(service.getGlassRule, { page: _ruleTablePageCurrent, results_per_page: _ruleTablePageCount })
        if (resp.code !== 0) {
          notification.error('获取glass开单建议规则失败')
          throw resp.msg
        }

        //处理数据
        resp = filterNull(resp)
        conditionData.forEach(val => {
          resp.data.forEach(row => {
            if (row[val.rule_field]) {
              row[val.rule_field] = JSON.parse(row[val.rule_field])['value']
            }
          })
          _ruleTableColum.push({
            ruleType: 'condition',
            key: val.rule_field,
            title: `${val.rule_field}${val.op_type}`,
            original: val
          })
        })
        updateData.forEach(val => {
          _ruleTableColum.push({
            ruleType: "update",
            key: val.rule_field,
            title: `${val.rule_field}`,
            original: val
          })
        })
        _ruleTableData = resp.data
        _ruleTablePageTotal = resp.num_results
        yield put({
          type: 'change',
          payload: {
            ruleTableData: _ruleTableData,
            ruleTableColum: _ruleTableColum,
            ruleTablePageTotal: _ruleTablePageTotal,
            ruleTablePageCurrent: _ruleTablePageCurrent,
            ruleTablePageCount: _ruleTablePageCount,
          }
        })
        yield put({
          type: 'change',
          payload: {
            definitionTableLoading: false,
          }
        })
       
      } catch (error) {
        console.error("获取规则列表失败", error)
        // notification.error(typeof error === 'string' ? error : '获取规则列表失败')
      }

    },

    *deleteRule({ payload, callback }, { call, select, put, context: { service } }) {
      try {
        let url = url = `/rule/glass/${payload.id}`
        let res = yield fetch(url, { method: 'delete' })
        if (res.code !== 0) {
          notification.error('删除规则失败')
          throw res.msg
        }
        notification.success("删除规则成功")
      } catch (error) {
        console.error("删除规则失败", error)
        // notification.error(typeof error === 'string' ? error : '删除规则失败')
      }
      callback()
    },

    *addRule({ payload, callback }, { call, select, put, context: { service } }) {
      let res;
      try {
        res = yield call(service.postGlassRule, payload.data)
        if (res.resp.code !== 0) {
          notification.error('创建规则失败')
          throw res.resp.msg
        }
        notification.success('创建规则成功')
        callback()
      } catch (error) {
        console.error("创建规则失败", error)
        // notification.error(typeof error === 'string' ? error : '创建规则失败')
        callback('error')
      }
    },
    *editRule({ payload, callback }, { call, select, put, context: { service } }) {
      try {
        let url = `/rule/glass/${payload.id}`
        let res = yield fetch(url, { method: 'put', body: payload.data })
        if (res.code !== 0) {
          notification.error('编辑规则失败')
          throw res.msg
        }
        notification.success('编辑规则成功')
        callback()
      } catch (error) {
        console.error("编辑规则失败", error)
        // notification.error(typeof error === 'string' ? error : '编辑规则失败')
        callback('error')
      }
    },

    *changePage({ payload, callback }, { call, select, put, context: { service } }) {
      let _payload = {}
      if (payload.ruleTablePageCount) {
        _payload.ruleTablePageCount = payload.ruleTablePageCount
      }
      if (payload.ruleTablePageCurrent) {
        _payload.ruleTablePageCurrent = payload.ruleTablePageCurrent
      }
      yield put({
        type: 'glass/change',
        payload: _payload
      })
      callback()
    }
  }
}
