import { TopTips } from '@tencent/bere'
import { NEW_SIGN } from '@constant/product'

function b64toBlob(b64String, contentType) {
  b64String = b64String.replace(/[\n\r]/g, '')
  const byteString = atob(b64String)
  const buffer = new ArrayBuffer(byteString.length)
  const array = new Uint8Array(buffer)
  for (let i = 0; i < byteString.length; i++) {
    array[i] = byteString.charCodeAt(i)
  }
  return new Blob([buffer], { type: contentType })
}

function uuid(len, radix) {
  const CHARS = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('')
  const chars = CHARS
  const uuid = []
  let i
  radix = radix || chars.length
  if (len) {
    for (i = 0; i < len; i++) {
      uuid[i] = chars[0 | (Math.random() * radix)]
    }
  } else {
    let r
    uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-'
    uuid[14] = '4'
    for (i = 0; i < 36; i++) {
      if (!uuid[i]) {
        r = 0 | (Math.random() * 16)
        uuid[i] = chars[i == 19 ? (r & 0x3) | 0x8 : r]
      }
    }
  }
  return uuid.join('')
}

function searchFilter(searchText, fileds) {
  return {
    or: fileds.map(name => ({ name, op: 'like', val: `%${searchText}%` }))
  }
}

function tableFilter(searchText, field) {
  return { name: field, op: 'eq', val: searchText }
}

export default {
  namespace: 'product',
  state: {
    // 数据总数
    total: 0,
    // 每页数量
    limit: 15,
    // 当前页数
    page: 0,
    // 搜索内容
    searchText: '',
    // 排序方式
    sortOrder: 'desc',
    // 过滤厂区
    siteFilter: null,
    // 过滤厂别
    shopFilter: null,
    // 产品列表是否加载中
    isProductLoading: false,
    // 是否正在提交表单
    isSubmitting: false,
    // 产品列表
    productList: [],
    // 检测项目列表
    jobsList: [],
    // 编辑模式
    editMode: null,
    // 产品 id
    id: '',
    // 厂区
    site: '',
    // 厂别
    shop: '',
    // 站点
    operation: '',
    // 产品
    product: '',
    // 放大倍率
    ratio: '',
    // 显示文件上传框
    fileUpload: false
  },

  reducers: {
    initialize(state) {
      return {
        ...state,
        page: 0,
        limit: 15,
        searchText: '',
        sortOrder: 'desc',
        siteFilter: null,
        shopFilter: null
      }
    },

    showFileUpload(state) {
      return { ...state, fileUpload: true }
    },

    closeFileUpload(state) {
      return { ...state, fileUpload: false }
    },

    showAdd(state) {
      return {
        ...state,
        editMode: 'add',
        id: '',
        site: '',
        shop: '',
        operation: '',
        product: '',
        ratio: ''
      }
    },

    showUpdate(state, { payload }) {
      const { product } = payload
      return {
        ...state,
        editMode: 'update',
        id: product.id,
        site: product.site,
        shop: product.factory_name,
        operation: product.process_operation_name,
        product: product.product_spec_name,
        ratio: product.ratio
      }
    },

    closeEdit(state) {
      return { ...state, editMode: null }
    },

    startSubmit(state) {
      return { ...state, isSubmitting: true }
    },

    endSubmit(state) {
      return { ...state, isSubmitting: false }
    }
  },

  effects: {
    // 刷新产品列表
    * refresh(action, { select, put }) {
      const { page, limit, searchText, sortOrder, siteFilter, shopFilter } = yield select(
        state => state.product
      )

      yield put({
        type: 'product/fetchProducts',
        payload: { page, limit, searchText, sortOrder, siteFilter, shopFilter }
      })
    },

    // 获取产品列表
    * fetchProducts(action, { select, put, call, cancel, context }) {
      yield put({
        type: 'product/change',
        payload: {
          productList: [],
          isProductLoading: true
        }
      })

      const { page, limit, searchText, sortOrder, siteFilter, shopFilter } = action.payload
      const params = {
        page: page + 1,
        results_per_page: limit,
        order_by: JSON.stringify({ field: 'modify_time', direction: sortOrder })
      }

      const q = {
        filters: [{ name: 'is_valid', op: 'eq', val: 1 }]
      }
      if (searchText) {
        q.filters.push(searchFilter(searchText, ['product_spec_name', 'process_operation_name']))
      }
      if (siteFilter) {
        q.filters.push(tableFilter(siteFilter, 'site'))
      }
      if (shopFilter) {
        q.filters.push(tableFilter(shopFilter, 'factory_name'))
      }
      params.q = JSON.stringify(q)

      const { resp } = yield call(context.service.getProductList, params)

      if (!resp.code) {
        if (page !== 0 && !resp.data[0]) {
          yield put({ type: 'product/change', payload: { page: page - 1 } })
          return yield put({ type: 'product/refresh' })
        }

        yield put({
          type: 'product/change',
          payload: {
            productList: resp.data,
            total: resp.num_results,
            isProductLoading: false
          }
        })
      }
    },

    // 添加站点产品
    * addProduct(action, { select, put, call, cancel, context }) {
      const { product, ratio, newSite, newShop, newOperation, siteMap } = action.payload

      let { site, shop, operation } = action.payload

      if (site === NEW_SIGN || shop === NEW_SIGN || operation === NEW_SIGN) {
        let curSite
        if (site === NEW_SIGN) {
          const { resp } = yield call(context.service.addSite, {
            site: newSite
          })
          if (!resp.code) {
            curSite = { ...resp.data, factory_list: [] }
            siteMap.push(curSite)
            site = newSite
          } else {
            yield put({ type: 'product/endSubmit' })
            return TopTips({
              theme: 'error',
              message: `厂区添加失败: ${resp.msg}`
            })
          }
        } else {
          curSite = siteMap.find(item => item.site === site)
        }

        let curShop
        if (shop === NEW_SIGN) {
          const { resp } = yield call(context.service.addShop, {
            correlate_site: curSite.id,
            factory_name: newShop
          })
          if (!resp.code) {
            curShop = { ...resp.data, pon_list: [] }
            curSite.factory_list.push(curShop)
            shop = newShop
          } else {
            yield put({ type: 'product/endSubmit' })
            return TopTips({
              theme: 'error',
              message: `厂别添加失败: ${resp.msg}`
            })
          }
        } else {
          curShop = curSite.factory_list.find(item => item.factory_name === shop)
        }

        if (operation === NEW_SIGN) {
          const { resp } = yield call(context.service.addOperation, {
            correlate_factory: curShop.id,
            process_operation_name: newOperation
          })
          if (!resp.code) {
            curShop.pon_list.push(resp.data)
            operation = newOperation
          } else {
            yield put({ type: 'product/endSubmit' })
            return TopTips({
              theme: 'error',
              message: `站点添加失败: ${resp.msg}`
            })
          }
        }
      }

      const { resp } = yield call(context.service.addProduct, {
        site,
        factory_name: shop,
        process_operation_name: operation,
        product_spec_name: product,
        ratio,
        correlate_detection: ''
      })

      yield put({
        type: 'product/change',
        payload: { editMode: null }
      })

      yield put({ type: 'product/endSubmit' })
      if (!resp.code) {
        TopTips({
          theme: 'success',
          message: '添加成功'
        })

        yield put({ type: 'product/refresh' })
      }
    },

    // 修改站点产品
    * updateProduct(action, { select, put, call, cancel, context }) {
      const { id, product, ratio, site, shop, operation } = action.payload
      const data = {
        id,
        site,
        factory_name: shop,
        process_operation_name: operation,
        product_spec_name: product,
        ratio
      }
      const { resp } = yield call(context.service.updateProduct, data)

      yield put({
        type: 'product/change',
        payload: { editMode: null }
      })

      if (resp.code === 2406) {
        yield put({ type: 'product/endSubmit' })
        return yield put({
          type: 'productManage/showConflictDialog',
          payload: {
            title: '修改站点产品',
            hint:
              '该站点产品已关联检测项目，修改操作可能导致原来的检测项目失效。' +
              '请先在 #{link}# 中取消关联后刷新尝试',
            data: [],
            keys: [],
            refreshAction: action
          }
        })
      }

      yield put({ type: 'product/endSubmit' })
      if (!resp.code) {
        TopTips({
          theme: 'success',
          message: '更新成功'
        })

        yield put({ type: 'product/refresh' })
      }
    },

    // 删除站点产品
    * deleteProduct(action, { select, put, call, cancel, context }) {
      const { id } = action.payload
      const { resp } = yield call(context.service.deleteProduct, { id })

      if (resp.code === 2406) {
        return yield put({
          type: 'productManage/showConflictDialog',
          payload: {
            title: '删除站点产品',
            hint:
              '该站点产品已关联检测项目，删除操作可能导致原来的检测项目失效。' +
              '请先在 #{link}# 中取消关联后刷新尝试',
            data: [],
            keys: [],
            refreshAction: action
          }
        })
      }

      if (!resp.code) {
        TopTips({
          theme: 'success',
          message: '删除成功'
        })
      }

      yield put({ type: 'product/refresh' })
    },

    // 批量修改站点产品（上传文件）
    * updateProducts(action, { select, put, call, cancel, context }) {
      const { data, md5 } = action.payload
      const { resp } = yield call(context.service.updateProducts, {
        session_uuid: uuid(),
        timestamp: Date.now(),
        data,
        file_format: 'xls',
        file_md5: md5,
        seq: 0,
        isEnd: 1
      })

      yield put({ type: 'product/closeFileUpload' })

      if (resp.code === 2406) {
        return yield put({
          type: 'productManage/showConflictDialog',
          payload: {
            title: '批量修改站点产品',
            hint:
              '下列 #{length}# 个站点产品已关联检测项目，修改操作可能导致原来的检测项目失效。' +
              '请先在 #{link}# 中取消关联后刷新尝试',
            data: resp.data,
            keys: [
              { key: 'site', title: '厂区' },
              { key: 'factory_name', title: '厂别' },
              { key: 'process_operation_name', title: '站点' },
              { key: 'product_spec_name', title: '产品' }
            ],
            refreshAction: action
          }
        })
      }

      if (!resp.code) {
        TopTips({ theme: 'success', message: '更新成功' })
      }

      yield put({ type: 'product/refresh' })
    },

    // 下载站点产品模板 excel
    * downloadProduct(action, { select, put, call, cancel, context }) {
      const { resp } = yield call(context.service.downloadProduct)
      if (!resp.code) {
        const link = document.createElement('a')
        let file
        try {
          file = b64toBlob(resp.data.content, 'application/vnd.ms-excel')
        } catch (e) {
          console.log(e)
        }
        if (window.navigator && window.navigator.msSaveOrOpenBlob) {
          window.navigator.msSaveOrOpenBlob(file, 'product.xls')
        } else {
          link.href = URL.createObjectURL(file)

          // link.href = 'data: application/vnd.ms-excel; base64,' + resp.data
          link.download = 'product.xls'
          link.click()
        }
      }
    },

    // 改变表格排序
    * changeSort(action, { put, select }) {
      let { sortOrder } = yield select(state => state.product)

      switch (sortOrder) {
      case 'desc':
        sortOrder = 'asc'
        break
      default:
        sortOrder = 'desc'
      }

      yield put({ type: 'product/change', payload: { sortOrder, page: 0 } })

      return yield put({ type: 'product/refresh' })
    },

    // 改变表格过滤条件
    * filterChange(action, { put }) {
      yield put({ type: 'product/change', payload: action.payload })
      return yield put({ type: 'product/refresh' })
    }
  }
}
