
import { notification } from '@tencent/bere'
export default {
  namespace: 'apply',
  state: {
    //应用配置按钮loading状态
    loading: false
  },
  effects: {
    *applySetting({ payload}, { select, put, call, cancel, context }) {
      yield put({
        type: 'change',
        payload: {
          loading: true,
        }
      })
      const {site, shop, version }=payload
      console.log("applySetting")
      console.log(site)
      try {
        const { resp } = yield call(context.service.applySetting,{site, shop, version })
       
        if (resp.code !== 0) {
          throw resp.msg
        }
        notification.success("应用配置成功")
      } catch (error) {
        notification.error(typeof error === 'string' ? error : '应用配置失败')
      }
      yield put({
        type: 'change',
        payload: {
          loading: false,
        }
      })
    }
  }
}
