import 'url-polyfill'
import {createPcApp, StrandardError} from '@tencent/tcff'
import {notification} from '@tencent/bere'
import urlMap from './config/urlMap'

import appModel from '@models/app'
import instanceModel from '@models/instance'

import loginModel from '@models/login'
import accountModel from '@models/account'
import productManageModel from '@models/productManage'
import rejudge from '@models/rejudge'
import rejudgeModel from '@models/rejudge3/index'
import historicalDataModel from '@models/historical_data/index'

import productModel from '@models/product'
import jobsModel from '@models/jobs'
import statisModel from '@models/statis'
import siteMapModel from '@models/siteMap'
import messageModel from '@models/message'
import dataPathModel from '@models/dataPath'
import glassModel from '@models/glass'
import imageModel from '@models/image'
import applyModel from '@models/apply'
import statisticalAnalysisModel from '@models/statistical_analysis'
import statisticalManageModel from '@models/statistical_manage'
import commonModel from '@models/common'
import error from '@constant/error'

import './static/less/index.less'

(function (arr) {
  arr.forEach(function (item) {
    if (item.hasOwnProperty('remove')) {
      return;
    }
    Object.defineProperty(item, 'remove', {
      configurable: true,
      enumerable: true,
      writable: true,
      value: function remove() {
        this.parentNode.removeChild(this);
      }
    });
  });
})([Element.prototype, CharacterData.prototype, DocumentType.prototype]);

const app = createPcApp('Ti-Matrix')

// 配置接口服务
app.service({ urlMap })
app.message.add(error)

// 配置应用通知组件
app.setNotification(notification)

app.use({
  onBeforeAjax(url, reqData, meta) {
    // console.log('===========================================================')
    // console.log('请求一次中间件')
    // console.log(url)
    // console.log(reqData)
    // console.log(meta)
    // console.log('===========================================================')
    // 包装请求
    meta.options.headers = {
      ...meta.options.headers,
      Authorization: `Bearer ${localStorage.getItem('accessToken')}` // 统一header token，Access_Token_xxxxx 从localstroge里取
    }

    /* if (url.indexOf('create') > -1 ){
      meta.options.headers = {
        ...meta.options.headers,
        "Content-Type": "multipart/form-data"
      }
    } */
  },
  onRouteChange({ app, query }) {
    // 如果本地并未保存密码，则跳转至登录页面
    if (localStorage.getItem('remember') !== 'true' &&
      app._history.location.pathname !== '/login'
    ) {
      app._history.replace('/login')
      return
    }
    // 设置当前实例ID
    if (query.instanceId !== null) {
      const { instance } = app._store.getState()
      if (query.instanceId === instance.currentId) {
        return
      }
      app._store.dispatch({
        type: 'instance/change',
        payload: {
          currentId: query.instanceId
        }
      })
    }
  },
  onAjaxDone(dataCode, method) {
    const {
      resp: { code }
    } = dataCode
    if (code == '401' || code == 401) {
      // 跳转至登录页，由于是通过判断loginSuccess是否为真执行操作，此次修改数据
      app._store.dispatch({
        type: 'login/change',
        payload: {
          loginSuccess: false
        }
      })
      localStorage.setItem('remember', false)
      this._history.replace('/login')
      console.log('token过期，跳转至登录页面')
    }
    if (code == 452) {
      notification.error('账户权限不足')
    }
    if (code >= 500 && code < 600) {
      notification.error('接口访问失败')
    }

    if (code < 0) {
      notification.error('后台服务不能访问')
    }
  }
})

app.model(require('./models/global').default)
app.model(appModel)
app.model(instanceModel)
app.model(productManageModel)
app.model(rejudge)
app.model(historicalDataModel)

app.model(rejudgeModel)
app.model(productModel)
app.model(jobsModel)
app.model(statisModel)
app.model(siteMapModel)
app.model(dataPathModel)
app.model(messageModel)
app.model(loginModel)
app.model(accountModel)
app.model(glassModel)
app.model(imageModel)
app.model(applyModel)
app.model(statisticalAnalysisModel)
app.model(statisticalManageModel)
app.model(commonModel)
// app.model(glassModel)
app.router(require('./router').default)

app.start('#app')
