import React, { PureComponent, Component } from 'react';
import { connect } from 'react-redux'
import moment from 'moment';
import groupBy from 'lodash/groupBy';
import Debounce from 'lodash-decorators/debounce';
import { Link } from 'react-router-dom'
import styles from './index.less';
import { Redirect } from 'react-router-dom';

@connect(({ login }) => ({
  loginSuccess: login.loginSuccess,
  user_name: login.user_name
}))
export default class GlobalHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user_name: '',
      isExitLogin: false
    };
  }

  componentDidMount() {
    if (this.state.user_name == null || this.state.user_name == '') {
      this.setState({
        user_name: localStorage.getItem("user_name") || '',
      });
    }
  }

  componentWillUnmount() {

  }

  // 退出登录
  handleExitLogin = () => {
    // console.log("执行退出操作");
    // 这里修改，取消登录状态
    this.props.dispatch({
      type: 'login/change',
      payload: {
        loginSuccess: false
      }
    });
    // 这里修改，取消记住密码
    localStorage.removeItem("remember");
    // 这里修改，取消token
    localStorage.removeItem("accessToken");
    // 这里修改，退出登录
    this.setState({
      isExitLogin: true
    });
  }


  render() {
    const { } = this.props;
    if (this.state.isExitLogin) {
      return (
        <Redirect to="/login" />
      );
    } else {
      return (
        <div className={styles.header}>
          <div className="qc-header-nav">
            <div className="qc-header-inner">
              <div className="qc-header-unit qc-header-logo" style={{"width":"300px"}}>
                <div className="qc-nav-logo">
                  <a href="##" title="腾讯云" className="qc-logo-inner">
                    <i className="qc-logo-icon"></i>腾讯云
                  </a> 
                  <span className="qc-logo-title">
                    <span>寻踪智能缺陷检测</span>
                  </span>
                </div>
              </div>
              <div className="qc-header-unit  qc-header-user">

                <div className="qc-nav-user">
                  <div className="qc-user-inner">
                    <span className="qc-user-name">{this.state.user_name}</span><i className="qc-nav-arrows"></i>
                    <div className="qc-user-info qc-user-panel">
                      <div className="qc-user-panel-inner">
                        {/* <a href="#">帐号信息</a> <a href="#">安全设置</a> <a href="#">访问管理</a> <a href="#">策略管理</a> <a href="#">项目管理</a> <a href="#">渠道管理</a> <a href="#">服务商管理</a> <a href="#">25端口解封</a> <span className="qc-user-info-line"></span> <a href="#">账户中心</a> <span className="qc-user-info-line"></span> <a href="#">切换开发商</a> <a href="#">退出</a> */}
                        <a style={{ paddingLeft: '10px' }} href="javascript:;" onClick={this.handleExitLogin}>退出登录</a>
                      </div>
                    </div>

                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>
      );
    }

  }
}
