
export default {
  namespace: 'siteMap',
  state: {
    // 站点关系
    siteMap: [],
    productList: []
  },
  reducers: {
  },
  effects: {
    * fetchSiteMap(action, { select, put, call, cancel, context }) {
      const { resp } = yield call(context.service.getSiteMap)

      if (resp.code === '0' || resp.code === 0) {
        yield put({
          type: 'siteMap/change',
          payload: { siteMap: resp.data }
        })
      }
    },
    * fetchProducts(action, { select, put, call, cancel, context }) {
    
      const { site, shop, operation } = action.payload
      const { resp } = yield call(context.service.getProductList, {
        q: JSON.stringify({
          filters: [
            {
              and: [
                { name: 'site', op: 'eq', val: site },
                {
                  and: [
                    { name: 'factory_name', op: 'eq', val: shop },
                    { name: 'process_operation_name', op: 'eq', val: operation }
                  ]
                }
              ]
            }
          ]
        })
      })
  
      if (resp.code === '0' || resp.code === 0) {
        let productList = resp.data
        .filter(({ is_valid }) => is_valid === 1)
        .map(item => ({
          text: item.product_spec_name,
          value: item.id
        }));
        yield put({
          type: 'siteMap/change',
          payload: {
            productList: productList
          }
        })
      }
    }
  }
}
