import { TopTips } from '@tencent/bere'
export default {
  namespace: 'productManage',
  state: {
    // 站点关系
    siteMap: [],
    isSiteMapLoading: true,
    // 厂区列表
    sites: [],
    // 厂别列表
    shops: [],
    conflict: {
      // 标题
      title: '',
      // 提示语
      hint: '',
      // 显示冲突提示
      visible: false,
      // 导致冲突的数据
      data: [],
      // 数据对应的表头
      keys: [],
      // 刷新的 action
      refreshAction: {}
    },
    confirm: {
      // 显示确认框
      visible: false,
      // 标题
      title: '',
      // 提示语
      hint: '',
      // 确认框文字
      okText: '',
      // 确认的 action
      confirmAction: {}
    }
  },
  reducers: {
    showConfirmDialog(state, { payload }) {
      return {
        ...state,
        confirm: {
          visible: true,
          ...payload
        }
      }
    },

    closeConfirmDialog(state) {
      return {
        ...state,
        confirm: {
          visible: false,
          title: '',
          hint: '',
          okText: '',
          confirmAction: {}
        }
      }
    },

    showConflictDialog(state, { payload }) {
      return {
        ...state,
        conflict: {
          visible: true,
          ...payload
        }
      }
    },

    closeConflictDialog(state) {
      return {
        ...state,
        conflict: {
          title: '',
          hint: '',
          visible: false,
          data: [],
          keys: [],
          refreshAction: {}
        }
      }
    }
  },
  effects: {
    // 获取厂区关系树
    * fetchSiteMap(action, { select, put, call, cancel, context }) {
      const { resp } = yield call(context.service.getSiteMap)

      if (!resp.code) {
        const siteMap = resp.data
        const sitesObj = {}
        const shopsObj = {}
        siteMap.forEach(site => {
          sitesObj[site.site] = 1
          if (site.factory_list) {
            site.factory_list.forEach(factory => {
              shopsObj[factory.factory_name] = 1
            })
          }
        })
        const sites = Object.keys(sitesObj).sort()
        const shops = Object.keys(shopsObj).sort()

        yield put({
          type: 'productManage/change',
          payload: { siteMap, isSiteMapLoading: false, shops, sites }
        })
      }
    }
  }
}
