import React, {Fragment} from 'react'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import DocumentTitle from 'react-document-title'
import GlobalHeader from '../components/GlobalHeader'

class NoSiderLayout extends React.PureComponent {

  constructor(props){
    super(props)
    this.state = {
    }
  }

  getPageTitle() {
    return 'NotFound';
  }

  render() {
    const {
      currentUser,
      match,
      location,
      children
    } = this.props;

    const layout = (
      <>
      <header>
        <GlobalHeader
        />
      </header>
      <div style={{left: 0}} className="container">
        {children}
      </div>
      </>
    );

    return (
      <DocumentTitle title={this.getPageTitle()}>
        {layout}
      </DocumentTitle>
    );
  }
}

export default connect(({ user, global, loading }) => ({
  currentUser: '',
}))(NoSiderLayout);
