export default {
  namespace: 'dataPath',
  state: {
    // 数据总数
    total: 0,
    // 每页数量
    limit: 15,
    // 当前页数
    page: 0,
    // 厂区过滤
    siteFilter: null,
    // 厂别过滤
    shopFilter: null,
    // 列表是否加载中
    isDataPathLoading: false,
    // 是否正在提交表单
    isSubmitting: false,
    showPathEditor: false,
    editing_field: '',
    // 列表
    pathList: [],
    // 检测项目列表
    jobsList: [],
    // 站点关系
    siteMap: [],
    // 编辑模式
    editMode: null,
    // 产品 id
    id: '',
    // 厂区
    site: '',
    // 厂别
    shop: '',
    // 公共路径
    public_path: '',
    // csv文件路径
    csv_path: '',
    // nas图片路径
    nas_img_path: '',
    // 图片 数据段:字段
    csv_img_path: '',
    // DEFECT_SIZE 数据段:字段
    csv_defect_size_path: '',
    csv_ratio_path: '',
    csv_archieve1_path: ''
  },

  reducers: {
    showAdd(state) {
      return {
        ...state,
        editMode: 'add',
        id: '',
        site: '',
        shop: '',
        public_path: '',
        csv_path: '',
        nas_img_path: '',
        csv_img_path: '',
        csv_defect_size_path: '',
        csv_ratio_path: '',
        csv_archieve1_path: ''
      }
    },

    startSubmit(state, action) {
      return { ...state, ...action.payload, isSubmitting: true }
    },

    endSubmit(state, action) {
      return { ...state, ...action.payload, isSubmitting: false }
    }
  },

  effects: {
    * refresh(action, { select, put }) {
      const { page, limit, siteFilter, shopFilter } = yield select(
        state => state.dataPath
      )

      yield put({
        type: 'dataPath/fetchPaths',
        payload: { page, limit, siteFilter, shopFilter }
      })
    },

    * fetchPaths(action, { select, put, call, cancel, context }) {
      yield put({
        type: 'dataPath/change',
        payload: {
          pathList: [],
          isDataPathLoading: true
        }
      })

      const { page, limit, siteFilter, shopFilter } = action.payload
      const params = {
        page: page + 1,
        results_per_page: limit
      }
      const q = { filters: [{ name: 'is_valid', op: 'eq', val: 1 }] }
      if (siteFilter) {
        q.filters.push({ name: 'site', op: 'eq', val: siteFilter })
      }
      if (shopFilter) {
        q.filters.push({ name: 'factory_name', op: 'eq', val: shopFilter })
      }
      params.q = JSON.stringify(q)

      const { resp } = yield call(context.service.getDataPathList, params)

      if (!resp.code) {
        if (page !== 0 && !resp.data[0]) {
          yield put({ type: 'dataPath/change', payload: { page: page - 1 } })
          return yield put({ type: 'dataPath/refresh' })
        }
        yield put({
          type: 'dataPath/change',
          payload: {
            pathList: resp.data,
            total: resp.num_results,
            isDataPathLoading: false
          }
        })
      }
    },
    * fetchPath(action, { select, put, call, cancel, context }) {
      yield put({
        type: 'dataPath/change',
        payload: {
          isJobsLoading: true,
          site: '',
          shop: '',
          public_path: '',
          csv_path: '',
          nas_img_path: '',
          csv_img_path: '',
          csv_defect_size_path: '',
          csv_ratio_path: '',
          csv_archieve1_path: ''
        }
      })

      const { id } = action.payload
      const { resp } = yield call(context.service.getDataPath, { id })

      if (resp.code === '0' || resp.code === 0) {
        const { data } = resp
        const { config } = data
        yield put({
          type: 'dataPath/change',
          payload: {
            site: data.site,
            shop: data.factory_name,
            public_path: data.public_path,
            csv_path: data.csv_path,
            nas_img_path: data.nas_img_path,
            csv_img_path: data.csv_img_path,
            csv_defect_size_path: data.csv_defect_size_path,
            csv_ratio_path: data.csv_ratio_path,
            csv_archieve1_path: data.csv_archieve1_path,
            isJobsLoading: false
          }
        })
      }
    },

    * fetchSiteMap(action, { select, put, call, cancel, context }) {
      const { resp } = yield call(context.service.getSiteMap)

      if (!resp.code) {
        yield put({
          type: 'dataPath/change',
          payload: { siteMap: resp.data }
        })
      }
    },

    * addPath(action, { select, put, call, cancel, context }) {
      const {
        site,
        shop,
        public_path,
        csv_path,
        nas_img_path,
        csv_img_path,
        csv_defect_size_path,
        csv_ratio_path,
        csv_archieve1_path
      } = action.payload

      const { resp } = yield call(context.service.addDataPath, {
        site,
        factory_name: shop,
        public_path,
        csv_path,
        nas_img_path,
        csv_img_path,
        csv_defect_size_path,
        csv_ratio_path,
        csv_archieve1_path
      })
      if (action.callback) {
        action.callback(resp)
      }
    },

    * updatePath(action, { select, put, call, cancel, context }) {
      const {
        id,
        site,
        shop,
        public_path,
        csv_path,
        nas_img_path,
        csv_img_path,
        csv_defect_size_path,
        csv_ratio_path,
        csv_archieve1_path
      } = action.payload
      const { resp } = yield call(context.service.updateDataPath, {
        id,
        site,
        factory_name: shop,
        public_path,
        csv_path,
        nas_img_path,
        csv_img_path,
        csv_defect_size_path,
        csv_ratio_path,
        csv_archieve1_path
      })
      if (action.callback) {
        action.callback(resp)
      }
    },

    * deletePath(action, { select, put, call, cancel, context }) {
      const { id } = action.payload
      const { resp } = yield call(context.service.deleteDataPath, { id })

      if (resp.code === 2406) {
        return yield put({
          type: 'productManage/showConflictDialog',
          payload: {
            title: '删除数据路径',
            hint:
              '该数据路径已关联检测项目，删除操作可能导致原来的检测项目失效。' +
              '请先在 #{link}# 中取消关联后刷新尝试',
            data: [],
            keys: [],
            refreshAction: action
          }
        })
      }

      if (action.callback) {
        action.callback(resp)
      }
    },

    * changeSort(action, { put, select }) {
      let { sortOrder } = yield select(state => state.product)

      switch (sortOrder) {
      case 'desc':
        sortOrder = 'asc'
        break
      default:
        sortOrder = 'desc'
      }

      yield put({ type: 'dataPath/change', payload: { sortOrder, page: 0 } })

      return yield put({ type: 'dataPath/refresh' })
    },

    * filterChange(action, { put }) {
      yield put({ type: 'dataPath/change', payload: action.payload })
      return yield put({ type: 'dataPath/refresh' })
    }
  }
}
