import { TopTips } from '@tencent/bere'
import jobs from '@models/jobs'

function b64toBlob(b64String, contentType) {
  const byteString = atob(b64String)
  const buffer = new ArrayBuffer(byteString.length)
  const array = new Uint8Array(buffer)
  for (let i = 0; i < byteString.length; i++) {
    array[i] = byteString.charCodeAt(i)
  }
  return new Blob([buffer], { type: contentType })
}

function normalizeReq({ method, url, data }, config = {}) {
  data = { ...data }

  const regex = /\/:(\w+)/g // 替换 url 参数
  const params = []
  let matches
  while ((matches = regex.exec(url)) != null) {
    if (matches[1]) {
      params.push(matches[1])
    }
  }
  params != null &&
    params.forEach(function (name) {
      let d = data[name]
      delete data[name]
      if (d == null) {
        d = ''
      }
      url = url.replace(`:${name}`, d)
    })

  if (data.q) {
    url += `?q=${data.q}`
    delete data.q
  }
  return { url, data }
}

function searchFilter(searchText, fileds) {
  return {
    or: fileds.map(name => ({ name, op: 'like', val: `%${searchText}%` }))
  }
}

export default {
  namespace: 'message',
  state: {
    // 数据总数
    total: 0,
    // 每页数量
    limit: 15,
    // 当前页数
    page: 0,
    // 过滤厂区
    siteFilter: null,
    // 排序方式
    sortOrder: 'desc',
    // 消息配置是否加载中
    isMessageLoading: false,
    // 是否正在提交表单
    isSubmitting: false,
    // 产品列表
    messageList: [],
    // 编辑模式
    editMode: null,
    // 站点关系
    siteMap: [],
    // 显示文件上传框
    fileUpload: false,
    // 消息 id
    id: '',
    // 厂区
    site: '',
    // ADC监听消息主题
    adc_listen: '',
    // ADC回复消息主题
    adc_response: '',
    // ADC监听消息角色
    listener: '',
    // ADC回复消息角色
    responser: '',
    listen_service: '',
    resp_service: '',
    listen_network: '',
    resp_network: '',
    listen_daemon: '',
    resp_daemon: ''
  },

  reducers: {
    initialize(state) {
      return {
        ...state,
        page: 0,
        limit: 15,
        searchText: '',
        sortOrder: 'desc',
        siteFilter: null
      }
    },

    initializeNewMessage(state) {
      return {
        ...state,
        id: '',
        site: '',
        adc_listen: '',
        adc_response: '',
        listener: '',
        responser: '',
        listen_service: '',
        resp_service: '',
        listen_network: '',
        resp_network: '',
        listen_daemon: '',
        resp_daemon: '',
        editMode: 'add'
      }
    },

    showFileUpload(state) {
      return { ...state, fileUpload: true }
    },

    closeFileUpload(state) {
      return { ...state, fileUpload: false }
    },

    showAdd(state) {
      return {
        ...state,
        editMode: 'add',
        id: '',
        site: '',
        adc_listen: '',
        adc_response: '',
        listener: '',
        responser: '',
        listen_service: '',
        resp_service: '',
        listen_network: '',
        resp_network: '',
        listen_daemon: '',
        resp_daemon: ''
      }
    },

    closeEdit(state) {
      return { ...state, editMode: null }
    },

    startSubmit(state) {
      return { ...state, isSubmitting: true }
    },

    endSubmit(state) {
      return { ...state, isSubmitting: false }
    }
  },

  effects: {
    * refresh(action, { select, put }) {
      const { page, limit, searchText, sortOrder, siteFilter } = yield select(
        state => state.message
      )

      yield put({
        type: 'message/fetchMessages',
        payload: { page, limit, searchText, sortOrder, siteFilter }
      })
    },

    * fetchMessages(action, { select, put, call, cancel, context }) {
      yield put({
        type: 'message/change',
        payload: {
          messageList: [],
          isMessageLoading: true
        }
      })

      const { page, limit, searchText, sortOrder, siteFilter } = action.payload
      const params = {
        page: page + 1,
        results_per_page: limit,
        order_by: JSON.stringify({ field: 'modify_time', direction: sortOrder })
      }

      const q = {
        filters: [{ name: 'is_valid', op: 'eq', val: 1 }]
      }
      if (searchText) {
        q.filters.push(
          searchFilter(searchText, [
            'adc_listen',
            'adc_response',
            'listen_daemon',
            'resp_daemon',
            'listen_service',
            'resp_service',
            'listen_network',
            'resp_network',
            'listener',
            'responser'
          ])
        )
      }
      if (siteFilter) {
        q.filters.push({ name: 'site', op: 'eq', val: siteFilter })
      }
      params.q = JSON.stringify(q)

      const { resp } = yield call(context.service.getMessageList, params)

      if (!resp.code) {
        if (page !== 0 && !resp.data[0]) {
          yield put({ type: 'message/change', payload: { page: page - 1 } })
          return yield put({ type: 'message/refresh' })
        }
        yield put({
          type: 'message/change',
          payload: {
            messageList: resp.data,
            total: resp.num_results,
            isMessageLoading: false
          }
        })
      }
    },

    * fetchMessage(action, { select, put, call, cancel, context }) {
      yield put({
        type: 'message/change',
        payload: {
          isMessageLoading: true,
          site: '',
          adc_listen: '',
          adc_response: '',
          listener: '',
          responser: '',
          listen_service: '',
          resp_service: '',
          listen_network: '',
          resp_network: '',
          listen_daemon: '',
          resp_daemon: ''
        }
      })

      const { id } = action.payload
      const { resp } = yield call(context.service.getMessage, { id })

      if (resp.code === '0' || resp.code === 0) {
        const { data } = resp
        yield put({
          type: 'message/change',
          payload: {
            isMessageLoading: false,
            site: data.site,
            adc_listen: data.adc_listen,
            adc_response: data.adc_response,
            listener: data.listener,
            responser: data.responser,
            listen_service: data.listen_service,
            resp_service: data.resp_service,
            listen_network: data.listen_network,
            resp_network: data.resp_network,
            listen_daemon: data.listen_daemon,
            resp_daemon: data.resp_daemon
          }
        })
      }
    },

    * fetchSiteMap(action, { select, put, call, cancel, context }) {
      const { resp } = yield call(context.service.getSiteMap)

      if (!resp.code) {
        yield put({
          type: 'message/change',
          payload: { siteMap: resp.data }
        })
      }
    },

    * addMessage(action, { select, put, call, cancel, context }) {
      const {
        site,
        adc_listen,
        adc_response,
        listener,
        responser,
        listen_service,
        resp_service,
        listen_network,
        resp_network,
        listen_daemon,
        resp_daemon
      } = action.payload

      const { resp } = yield call(context.service.addMessage, {
        site,
        adc_listen,
        adc_response,
        listener,
        responser,
        listen_service,
        resp_service,
        listen_network,
        resp_network,
        listen_daemon,
        resp_daemon
      })
      if (!resp.code) {
        TopTips({
          theme: 'success',
          message: '添加成功'
        })
        yield put({
          type: 'message/change',
          payload: { editMode: null }
        })
        context.history.replace('/product/message')
      }

      yield put({ type: 'message/endSubmit' })

    },

    * updateMessage(action, { select, put, call, cancel, context }) {
      const {
        id,
        site,
        adc_listen,
        adc_response,
        listener,
        responser,
        listen_service,
        resp_service,
        listen_network,
        resp_network,
        listen_daemon,
        resp_daemon
      } = action.payload

      const { resp: jobsResp } = yield call(context.service.getJobsList, {
        q: JSON.stringify({
          filters: [
            {
              or: [
                { name: 'config', op: 'like', val: `%'site': '${site}'%` },
                { name: 'config', op: 'like', val: `%"site":"${site}"%` }
              ]
            }
          ]
        })
      })

      if (jobsResp.code) {
        yield put({ type: 'message/endSubmit' })
        return TopTips({ theme: 'error', message: '检测项目获取失败' })
      }

      for (let i = 0; i < jobsResp.data.length; i++) {
        const job = jobsResp.data[i]
        yield call(
          context.service.updateJob,
          {
            id: job.id,
            config: {
              ...job.config,
              monitor: {
                service: listen_service,
                network: listen_network,
                daemon: listen_daemon,
                topic: adc_listen,
                listener: listener
              },
              reply: {
                service: resp_service,
                network: resp_network,
                daemon: resp_daemon,
                topic: adc_response,
                listener: responser
              }
            }
          },
          { normalizeReq }
        )
      }

      const { resp } = yield call(context.service.updateMessage, {
        id,
        site,
        adc_listen,
        listener,
        adc_response,
        responser,
        listen_service,
        resp_service,
        listen_network,
        resp_network,
        listen_daemon,
        resp_daemon
      })

      if (!resp.code) {
        TopTips({
          theme: 'success',
          message: '更新成功'
        })
        yield put({
          type: 'message/change',
          payload: { editMode: null }
        })
        context.history.replace('/product/message')
      }

      yield put({ type: 'message/endSubmit' })
    },

    * deleteMessage(action, { select, put, call, cancel, context }) {
      const { id } = action.payload
      const { resp } = yield call(context.service.deleteMessage, { id })

      if (resp.code === 2406) {
        return yield put({
          type: 'productManage/showConflictDialog',
          payload: {
            title: '删除消息配置',
            hint:
              '该消息配置已关联检测项目，删除操作可能导致原来的检测项目失效。' +
              '请先在 #{link}# 中取消关联后刷新尝试',
            data: [],
            keys: [],
            refreshAction: action
          }
        })
      }

      if (!resp.code) {
        TopTips({
          theme: 'success',
          message: '删除成功'
        })
      }

      yield put({ type: 'message/refresh' })
    },

    * updateMessages(action, { select, put, call, cancel, context }) {
      const { data, md5 } = action.payload
      const { resp } = yield call(context.service.updateMessages, {
        session_uuid: '',
        timestamp: Date.now(),
        data,
        file_format: 'xls',
        file_md5: md5,
        seq: 0,
        isEnd: 1
      })

      yield put({ type: 'message/closeFileUpload' })
      if (resp.code === 2406) {
        return yield put({
          type: 'productManage/showConflictDialog',
          payload: {
            title: '修改消息配置',
            hint:
              '下列 #{length}# 个消息配置已关联检测项目，修改操作可能导致原来的检测项目失效。' +
              '请先在 #{link}# 中取消关联后刷新尝试',
            data: resp.data,
            keys: [{ key: 'site', title: '厂区' }],
            refreshAction: action
          }
        })
      }

      if (!resp.code) {
        TopTips({ theme: 'success', message: '更新成功' })
      }

      yield put({ type: 'message/refresh' })
    },

    * downloadMessage(action, { select, put, call, cancel, context }) {
      const { resp } = yield call(context.service.downloadMessage)
      if (!resp.code) {
        const link = document.createElement('a')
        const file = b64toBlob(resp.data.content, 'application/vnd.ms-excel')
        link.href = URL.createObjectURL(file)

        // link.href = 'data: application/vnd.ms-excel; base64,' + resp.data
        link.download = 'message.xls'
        link.click()
      }
    },

    * changeSort(action, { put, select }) {
      let { sortOrder } = yield select(state => state.message)

      switch (sortOrder) {
      case 'desc':
        sortOrder = 'asc'
        break
      default:
        sortOrder = 'desc'
      }

      yield put({ type: 'message/change', payload: { sortOrder, page: 0 } })

      return yield put({ type: 'message/refresh' })
    },

    * filterChange(action, { put }) {
      yield put({ type: 'message/change', payload: action.payload })
      return yield put({ type: 'message/refresh' })
    }
  }
}
