function enhanceAppReady(Component) {
  class AppReady extends React.Component {
    handleWindowResize = _.debounce(
      {},
      () => {
        this.props.dispatch({
          type: 'app/change',
          payload: {
            windowHeight: window.innerHeight
          }
        })
      },
      100
    )

    componentDidMount() {
      window.addEventListener('resize', this.handleWindowResize)
      this.props.dispatch({
        type: 'app/ready'
      })
    }

    componentWillUnmount() {
      window.removeEventListener('resize', this.handleWindowResize)
    }

    render() {
      if (this.props.isAppReady) {
        return <Component {...this.props} />
      } else {
        return <PageLoading />
      }
    }
  }

  const mapStateToProps = state => {
    return {
      isAppReady: true,
      windowHeight: state.app.windowHeight
    }
  }

  return connect(mapStateToProps)(AppReady)
}

