import { mockData, initState, mockImage } from '@models/statistical_analysis/state'
import { reducers } from '@models/statistical_analysis/reducers'
import effects  from '@models/statistical_analysis/effects'

export default {
  namespace:'statisticalAnalysisModel',
  state: initState,
  reducers: reducers,
  /**
   * action 包含了dispatch 传过来的参数
   * put 调用 reducers的函数
   * context react的对象
   * call ajax请求的方法
   * 内置了一个 reducers的 change 函数
   * */
  effects: effects
}
