import {initState} from '@models/rejudge3/state'

export const reducers = {
  Initialize(state, action) {
    return {...initState, ...action}
  },
  // 改变第一层的数据
  CHANGE_BASE_STATE(state, action) {
    return {...state, ...action}
  },

  // R_T_S(state, action) {
  //   return {
  //     ...state, imageInfo: {...state.imageInfo, ...action},
  //   }
  // },
  CHOOSE_CODE_BTN(state, action) {
    return {
      ...state, imageInfo: {...state.imageInfo, valid_code: action.valid_code},
      codeBtnActiveNumber: action.codeBtnActiveNumber
    }
  },

}
