export function filterNull(res) {
  let _res = JSON.parse(JSON.stringify(res))

  _res.data.forEach(col => {
    let _rule = col["rule"];
    if(_rule){
      _rule = JSON.parse(_rule);
      for(let k in _rule){
        col[k] = _rule[k];
      }
    }
    for (const key in col) {
      if (col[key] == null || key=="rule") {
        delete col[key]
      }
    }
  })
  return _res
}