import { TopTips } from '@tencent/bere'
import { jobsOperation } from '@constant/jobs'

// 在 get 以外的请求中需要带 queryString 时
// 使用这个函数覆盖 tcff 原本的 normalizeReq
// 然后将 queryString 作为 q 属性放在 data 中
function normalizeReq({ method, url, data }, config = {}) {
  data = { ...data }

  const regex = /\/:(\w+)/g // 替换 url 参数
  const params = []
  let matches
  while ((matches = regex.exec(url)) != null) {
    if (matches[1]) {
      params.push(matches[1])
    }
  }
  params != null &&
    params.forEach(function (name) {
      let d = data[name]
      delete data[name]
      if (d == null) {
        d = ''
      }
      url = url.replace(`:${name}`, d)
    })

  if (data.q) {
    url += `?q=${data.q}`
    delete data.q
  }
  return { url, data }
}

function searchFilter(searchText, fileds) {
  return {
    or: fileds.map(name => ({ name, op: 'like', val: `%${searchText}%` }))
  }
}

export default {
  namespace: 'jobs',
  state: {
    // 已选择的任务
    selectedJobs: [],
    // 任务数据
    jobsList: [],
    // 数据总数
    total: 0,
    // 每页数量
    limit: 15,
    // 当前页数
    page: 0,
    // 排序方式
    sortOrder: null,
    // 加载中
    isJobsLoading: false,
    // 是否正在提交表单
    isSubmitting: false,
    // 搜索框内容
    searchText: '',
    // 对话框
    dialog: {
      // 是否显示对话框
      visible: false,
      // 对话框对应操作
      operation: '',
      // 是否为批量操作
      batch: false,
      // 对话框确认后 dispatch 的 action
      action: null
    },
    // 显示缺少消息配置的对话框
    showMessageDialog: false,
    // 厂区关系树
    siteMap: [],
    // 产品列表
    productList: [],
    // 产品搜索内容
    productSearch: '',
    // 服务算法列表
    templateList: [],
    // 当前的编辑模式
    editMode: 'new',
    // 项目id
    id: '',
    // 项目名称
    name: '',
    // 项目名称填写是否错误
    nameError: false,
    // 布控起始时间
    scheduleStartTime: '',
    // 布控结束时间
    scheduleEndTime: '',
    // 布控时间是否固定
    schedule: 0,
    // 服务算法 id
    templateId: '',
    // 厂区
    site: '',
    // 厂别
    shop: '',
    // 站点
    operation: '',
    // 产品
    products: [],
    // 消息配置
    monitor: {},
    reply: {},
    // 启用状态
    status: 'START'
  },

  reducers: {
    initialize(state) {
      return { ...state, page: 0, limit: 15, searchText: '', sortOrder: 'desc' }
    },

    initializeNewJob(state) {
      return {
        ...state,
        editMode: 'new',
        name: '',
        scheduleStartTime: '',
        scheduleEndTime: '',
        schedule: 0,
        templateId: '',
        siteMap: [],
        site: '',
        shop: '',
        operation: '',
        products: [],
        productList: []
      }
    },

    openDialog(state, { payload }) {
      const { operation, action, batch } = payload
      return {
        ...state,
        dialog: {
          visible: true,
          operation,
          batch,
          action
        }
      }
    },

    openMessageDialog(state) {
      return { ...state, showMessageDialog: true }
    },

    closeDialog(state) {
      return {
        ...state,
        showMessageDialog: false,
        dialog: {
          visible: false,
          operation: '',
          batch: false,
          action: null
        }
      }
    },

    startSubmit(state) {
      return { ...state, isSubmitting: true }
    },

    endSubmit(state) {
      return { ...state, isSubmitting: false }
    }
  },

  effects: {
    // 刷新任务列表
    * refresh(action, { select, put }) {
      const { page, limit, searchText, sortOrder } = yield select(state => state.jobs)

      yield put({
        type: 'jobs/fetchJobs',
        payload: { page, limit, searchText, sortOrder }
      })
    },

    // 获取任务列表
    * fetchJobs(action, { select, put, call, cancel, context }) {
      yield put({
        type: 'change',
        payload: {
          jobsList: [],
          isJobsLoading: true
        }
      })

      const { page, limit, searchText, sortOrder } = action.payload
      const params = {
        page: page + 1,
        results_per_page: limit
      }

      const q = {}
      if (sortOrder) {
        q.order_by = [{ field: 'cTime', direction: sortOrder }]
      }
      if (searchText) {
        q.filters = [searchFilter(searchText, ['name', 'id'])]
      }
      params.q = JSON.stringify(q)

      const { resp } = yield call(context.service.getJobsList, params)

      if (resp.code === '0' || resp.code === 0) {
        if (page !== 0 && !resp.data[0]) {
          yield put({ type: 'jobs/change', payload: { page: page - 1 } })
          return yield put({ type: 'jobs/refresh' })
        }
        yield put({
          type: 'jobs/change',
          payload: {
            jobsList: resp.data,
            selectedJobs: [],
            searchText,
            total: resp.num_results,
            isJobsLoading: false
          }
        })
      }
    },

    // 根据id获取单个任务信息
    * fetchJob(action, { select, put, call, cancel, context }) {
      yield put({
        type: 'jobs/change',
        payload: {
          isJobsLoading: true,
          name: '',
          scheduleStartTime: '',
          scheduleEndTime: '',
          schedule: 0,
          templateId: '',
          site: '',
          shop: '',
          operation: '',
          products: []
        }
      })

      const { id } = action.payload
      const { resp } = yield call(context.service.getJob, { id })

      if (resp.code === '0' || resp.code === 0) {
        const { data } = resp
        const { config } = data
        yield put({
          type: 'jobs/change',
          payload: {
            name: data.name,
            scheduleStartTime: data.scheduleStartTime,
            scheduleEndTime: data.scheduleEndTime,
            schedule: data.schedule,
            templateId: data.templateId,
            site: config.site,
            shop: config.shop,
            operation: config.operation,
            products: config.products,
            monitor: config.monitor,
            reply: config.reply,
            status: data.status,
            isJobsLoading: false
          }
        })

        yield put({
          type: 'jobs/fetchProducts',
          payload: config
        })
      }
    },

    // 批量停止任务
    * stopJobs(action, { select, put, call, cancel, context }) {
      const { id } = action.payload
      const { resp } = yield call(context.service.updateJobs, {
        q: { filters: [{ name: 'id', op: 'in', val: id }] },
        status: 'STOP'
      })

      if (resp.code === '0' || resp.code === 0) {
        TopTips({
          theme: 'done',
          message: jobsOperation.stop.successMessage
        })
      }

      yield put({ type: 'jobs/refresh' })
    },

    // 批量启用任务
    * startJobs(action, { select, put, call, cancel, context }) {
      const { id } = action.payload

      const { resp } = yield call(context.service.updateJobs, {
        q: { filters: [{ name: 'id', op: 'in', val: id }] },
        status: 'START'
      })
      if (resp.code === '0' || resp.code === 0) {
        TopTips({
          theme: 'done',
          message: jobsOperation.start.successMessage
        })
      }

      yield put({ type: 'jobs/refresh' })
    },

    // 批量删除任务
    * deleteJobs(action, { select, put, call, cancel, context }) {
      const { id } = action.payload

      const { resp } = yield call(
        context.service.deleteJobs,
        {
          q: encodeURIComponent(JSON.stringify({ filters: [{ name: 'id', op: 'in', val: id }] }))
        },
        { normalizeReq }
      )

      if (resp.code === '0' || resp.code === 0) {
        TopTips({
          theme: 'done',
          message: jobsOperation.delete.successMessage
        })
      }

      yield put({ type: 'jobs/refresh' })
    },

    // 停止单个任务
    * stopJob(action, { select, put, call, cancel, context }) {
      const { id } = action.payload
      const { resp } = yield call(
        context.service.updateJob,
        {
          id,
          status: 'STOP'
        },
        { normalizeReq }
      )

      if (resp.code === '0' || resp.code === 0) {
        TopTips({
          theme: 'done',
          message: jobsOperation.stop.successMessage
        })
      }

      yield put({ type: 'jobs/refresh' })
    },

    // 启用单个任务
    * startJob(action, { select, put, call, cancel, context }) {
      const { id } = action.payload

      const { resp } = yield call(
        context.service.updateJob,
        {
          id,
          status: 'START'
        },
        { normalizeReq }
      )
      if (resp.code === '0' || resp.code === 0) {
        TopTips({
          theme: 'done',
          message: jobsOperation.start.successMessage
        })
      }

      yield put({ type: 'jobs/refresh' })
    },

    // 删除单个任务
    * deleteJob(action, { select, put, call, cancel, context }) {
      const { id } = action.payload

      const { resp } = yield call(context.service.deleteJob, { id })

      if (resp.code === '0' || resp.code === 0) {
        TopTips({
          theme: 'done',
          message: jobsOperation.delete.successMessage
        })
      }

      yield put({ type: 'jobs/refresh' })
    },

    // 获取厂区关系树
    * fetchSiteMap(action, { select, put, call, cancel, context }) {
      const { resp } = yield call(context.service.getSiteMap)

      if (resp.code === '0' || resp.code === 0) {
        yield put({
          type: 'jobs/change',
          payload: { siteMap: resp.data }
        })
      }
    },

    // 获取指定厂区厂别站点下的产品
    * fetchProducts(action, { select, put, call, cancel, context }) {
      const { site, shop, operation } = action.payload
      const { resp } = yield call(context.service.getProductList, {
        q: JSON.stringify({
          filters: [
            {
              and: [
                { name: 'site', op: 'eq', val: site },
                {
                  and: [
                    { name: 'factory_name', op: 'eq', val: shop },
                    { name: 'process_operation_name', op: 'eq', val: operation }
                  ]
                }
              ]
            }
          ]
        })
      })

      if (resp.code === '0' || resp.code === 0) {
        yield put({
          type: 'jobs/change',
          payload: {
            productList: resp.data.filter(({ is_valid }) => is_valid === 1).map(item => ({
              product: item.product_spec_name,
              ratio: item.ratio
            }))
          }
        })
      }
    },

    // 添加任务
    * addJob(action, { select, put, call, cancel, context }) {
      const {
        name,
        scheduleStartTime,
        scheduleEndTime,
        schedule,
        templateId,
        site,
        shop,
        operation,
        products
      } = action.payload

      const { resp: messageResp } = yield call(context.service.getMessageList, {
        q: JSON.stringify({
          filters: [
            {
              and: [{ name: 'site', op: 'eq', val: site }, { name: 'is_valid', op: 'eq', val: 1 }]
            }
          ]
        })
      })

      if (messageResp.code) {
        yield put({ type: 'jobs/endSubmit' })
        return TopTips({ theme: 'error', message: '获取消息配置失败' })
      }

      if (!messageResp.data[0]) {
        yield put({ type: 'jobs/endSubmit' })
        return yield put({ type: 'jobs/openMessageDialog' })
      }

      const message = messageResp.data[0]

      const { resp } = yield call(context.service.addJob, {
        config: {
          site,
          shop,
          operation,
          monitor: {
            service: message.listen_service,
            network: message.listen_network,
            daemon: message.listen_daemon,
            topic: message.adc_listen,
            listener: message.listener
          },
          reply: {
            service: message.resp_service,
            network: message.resp_network,
            daemon: message.resp_daemon,
            topic: message.adc_response,
            listener: message.responser
          },
          products
        },
        name,
        scheduleStartTime,
        scheduleEndTime,
        schedule,
        dataSource: [{ id: '', parentgroup: '' }],
        templateId,
        status: 'START'
      })

      if (resp.code === '0' || resp.code === 0) {
        TopTips({
          theme: 'done',
          message: jobsOperation.add.successMessage
        })
      }

      yield put({ type: 'jobs/endSubmit' })
      context.history.replace('/jobs')
    },

    // 更新任务
    * updateJob(action, { select, put, call, cancel, context }) {
      const {
        id,
        name,
        scheduleStartTime,
        scheduleEndTime,
        schedule,
        templateId,
        site,
        shop,
        operation,
        monitor,
        reply,
        status,
        products
      } = action.payload
      const { resp } = yield call(
        context.service.updateJob,
        {
          id,
          config: { site, shop, operation, products, monitor, reply },
          name,
          scheduleStartTime,
          scheduleEndTime,
          schedule,
          dataSource: [{ id: '', parentgroup: '' }],
          templateId,
          status
        },
        { normalizeReq }
      )

      if (resp.code === '0' || resp.code === 0) {
        TopTips({ theme: 'done', message: jobsOperation.update.successMessage })
      }

      yield put({ type: 'jobs/endSubmit' })
      context.history.replace('/jobs')
    },

    // 获取算法模板列表
    * fetchTemplates(action, { select, put, call, cancel, context }) {
      const { resp } = yield call(context.service.getTemplateList)

      if (resp.code === '0' || resp.code === 0) {
        yield put({ type: 'jobs/change', payload: { templateList: resp.data } })
      }
    },

    // 改变表格排序
    * changeSort(action, { put, select }) {
      let { sortOrder } = yield select(state => state.jobs)

      switch (sortOrder) {
      case 'desc':
        sortOrder = 'asc'
        break
      case 'asc':
        sortOrder = null
        break
      default:
        sortOrder = 'desc'
      }

      yield put({ type: 'jobs/change', payload: { sortOrder, page: 0 } })

      return yield put({ type: 'jobs/refresh' })
    }
  }
}
