import _ from 'lodash/fp'
import moment from 'moment'
import { getFormValues, getFormSyncErrors, change } from 'redux-form'
import { STATUS_TITLE } from '@config/instance'
import { DATE_TIME_FORMAT } from '@config/date'
import { delay } from '@utils/promise'


export default {
  namespace: 'instance',
  state: {
    list: [],
    params: [], // 当前拉取的参数
    total: 0,
    page: 0,
    pageSize: 20,
    orderKey: 'createTime',
    orderType: 'desc',
    currentId: null,
    current: null,
    listSilentLoading: false,
    usedStorage: 0,
    loadTaskError: null,
    loadingTask: false,
    pollingTask: false
  },
  effects: {
    * fetch(action, {select, put, call, cancel, context: {service}}) {
      console.log("111");
       const { resp } = yield call(service.getInstanceList,  {level: 0})
      if (resp.success) {
        const { totalCnt, instances } = resp.data
        yield put({
          type: 'change',
          payload: {
            total: totalCnt,
            listSilentLoading: false
          }
        })
      } 
    },
    * fetchDetail(action, {select, put, call, context: {service}}) {
      /* const { currentId } = yield select(_.prop('instance'))
      const params = {
        instanceId: currentId
      }
      const { resp } = yield call(service.getInstanceDetail, params)
      if (resp.success) {
        const { instanceDetail } = resp.data
        yield put({
          type: 'change',
          payload: {
            currentId: instanceDetail.instanceId
          }
        })

        // 拉取用量
        yield put({
          type: 'fetchUsedStorage',
          payload: {
            instanceId: currentId
          }
        })

        yield put({
          type: 'fetchParams'
        })

        yield put({
          type: 'instance/startPollInstanceTask'
        })
      } */
    },
    * fetchParams(action, {put, select, call, context: {service}}) {
      const params = yield select(({instance}) => ({
        instanceId: instance.currentId,
        needReboot: 1
      }))
      const {resp} = yield call(service.getInstanceParams, params)
      if (resp.success) {
        yield put({
          type: 'change',
          payload: {
            params: resp.data.params
          }
        })
      }
    },
    * fetchUsedStorage(action, {put, select, call, context: {service}}) {
      const now = moment()
      const params = yield select(({instance}) => ({
        instanceId: instance.currentId,
        startTime: moment(now).subtract(2, 'minutes').format(DATE_TIME_FORMAT),
        endTime: now.format(DATE_TIME_FORMAT),
        metricName: ['real_capacity']
      }))
      const { resp } = yield call(service.getMonitorData, params)
      if (resp.success) {
        const { metric } = resp.data
        const usedStorage = _.first(metric)
        yield put({
          type: 'change',
          payload: {
            usedStorage
          }
        })
      }
    },
    * pollInstanceTask(action, {put, select, call, context: {service}}) {
      const { currentId, current } = yield select(_.prop('instance'))
      const params = {
        instanceId: currentId
      }
      while (true) {
        yield put({
          type: 'change',
          payload: {
            loadingTask: true,
            pollingTask: true
          }
        })
        const { resp } = yield call(service.getInstanceTask, params)
        if (resp.success) {
          const { desc } = resp.data
          yield put({
            type: 'change',
            payload: {
              current: {
                ...current,
                lockDesc: desc
              },
              loadingTask: false
            }
          })
        } else {
          yield put({
            type: 'change',
            payload: {
              loadTaskError: '拉取任务失败',
              loadingTask: false
            }
          })
        }
        yield call(delay, 5000)
      }
    },
    * cancelPollInstanceTask(action, {put, select}) {
      yield put({
        type: 'change',
        payload: {
          loadingTask: false,
          pollingTask: false,
          loadTaskError: null
        }
      })
    },
    watchPollInstanceTask: [
      function * ({take, race, put, callAction}) {
        while (true) {
          yield take('instance/startPollInstanceTask')
          yield race([
            callAction({type: 'pollInstanceTask'}),
            take('instance/cancelPollInstanceTask')
          ])
        }
      },
      {type: 'watcher'}
    ],
    * filterBySearchKey({payload}, {put}) {
      yield put({
        type: 'fetch'
      })
    },
    * filterByPage({payload}, {put}) {
      yield put({
        type: 'change',
        payload: {
          page: payload.page
        }
      })
      yield put({type: 'fetch'})
    },
    * filterByPageSize({payload}, {put}) {
      yield put({
        type: 'change',
        payload: {
          page: 0,
          pageSize: payload.pageSize
        }
      })
      yield put({type: 'fetch'})
    },
    * resetFetch(action, {put}) {
      yield put(change('instanceListAction', 'searchKey', ''))
      yield put({
        type: 'change',
        payload: {
          page: 0
        }
      })
      yield put({
        type: 'fetch'
      })
    },
    * silentFetch(action, {put}) {
      yield put({
        type: 'change',
        payload: {
          listSilentLoading: true
        }
      })
      yield put({
        type: 'fetch'
      })
    }
  }
}
