import timg1 from '@modules/testImg/timg.jpg'
import timg2 from '@modules/testImg/timg2.jpg'
import timg3 from '@modules/testImg/timg3.jpg'
import timg4 from '@modules/testImg/timg4.jpg'
import timg5 from '@modules/testImg/timg5.jpg'

import rejudge from '@config/rejudge'

export const mockData = rejudge.table.mockData;

// 有N张图片
const imageMap = [timg1,timg2,timg3,timg4,timg5]

// 给每一张图片分配一组信息
export const imageInfos = [];

for (let i = 0; i < imageMap.length; i++) {
  if (i%2 === 0) {
    imageInfos.push({
      code: `SPASD` + i, // 图片的code类型
      create_time: '', //
      id: `code-${i}`,
      img_name: '', //
      img_src_path: imageMap[i], //
      img_src_time: '', //
      img_store_path: '', //
      modify_time: '', //
      op: '', //
      sample_id: '', //
      status: '', //
      valid_code: '', //
      rework: true, // 是否返工
      turn_on: false, //
      sheet_type: true, // 是否开具异常单
      // code_type: codeTypeInfos() // code类型的集合
    })
  } else {
    imageInfos.push({
      code: '', // 图片的code类型
      create_time: '', //
      id: `code-${i}`,
      img_name: '', //
      img_src_path: '', //
      img_src_time: '', //
      img_store_path: '', //
      modify_time: '', //
      op: '', //
      sample_id: '', //
      status: '', //
      valid_code: '', //
      imageUrl: imageMap[i],
      codeType: ``, // 图片的code类型
      rework: false, // 是否返工
      turn_on: false, //
      sheet_type: false, // 是否开具异常单
      // code_type: codeTypeInfos() // code类型的集合
    })
  }
}
export function codeTypeInfos () {
  let _arr = [];
  for(let i = 0; i < 10 ; i++) {
    _arr.push({id: 'a' + i, codeType: 'SPASD'+i})
  }
  return _arr
}

