import rejudgeCfg from '@config/rejudge'
import {confirm, TopTips} from '@tencent/bere'

const {MODEL, parentPath, testImageUrl, codeBtnRowNumber} = rejudgeCfg;


/**
 * 用来判断是否打开了切换图片的警告框
 * */
let modalFlagIndex = 0;

/**
 * 快捷键选择code的时候记录的下标
 * */

const utils = {
  /**
   * 获取图片详情的code列表
   * @param that class
   * */
  queryCode(that) {
    that.props.dispatch({
      type: `${MODEL}/queryCode`,
      callback: (res) => {
        if (res.result) {}
      }
    })
  },
  /**
   * 处理键盘事件
   * @param this class
   * @param window.event
   * */
  keyboardEvent,
  /**
   * 切换系统的时候，赋予正确的系统值
   * @param that
   * */
  localSystem(that) {
    let path = that.props.path
    let index = path.lastIndexOf("\/")
    let sys = path.substring(index + 1, path.length)
    that.props.dispatch({
      type: `${MODEL}/switchSystem`,
      data: {
        sysActive: sys,
        rejudgeType: rejudgeCfg.rejudgeType[sys][0].value
      }
    })
  },
  /**
   * 切换系统
   * @param that
   * @param val
   * */
  switchSystem(that, val){
    let { startTask, uploadState } = that.props
    if (startTask || uploadState) {
      confirm({
        caption: `提示`,
        text: `任务正在进行中，如果需要切换系统，请先“结束复判”`,
        type: 'warning'
      }).then(result => {
        if(result === 'cancel'){} else {}
      });
      return
    }
    let _rejudge = val === 'sampleSys' ?  'file' : 'product'
    that.props.dispatch({
      type: `${MODEL}/initialize`,
      data: {
        sysActive: val,
        rejudgeType: _rejudge,
        // ...initQueryOrInfo
      }
    })
    that.props.history.push(parentPath + val)
  },
  switchRejudgeType(that, val) {
    let { startTask, uploadState } = that.props
    if (startTask || uploadState) {
      confirm({
        caption: `提示`,
        text: `任务正在进行中，如果需要切换复判类型，请先“结束复判”`,
        type: 'warning'
      }).then(result => {
        if(result === 'cancel'){} else {}
      });
      return
    }
    that.props.dispatch({
      type: `${MODEL}/initialize`,
      data: {
        sysActive: that.props.sysActive,
        rejudgeType:val,
        // ...initQueryOrInfo
      }
    })
  },
  /**
   * 切换图片的时候进行的动作，
   * @param that this
   * @param funcName previousImg上一张，nextImg下一张
   * @param chooseCode 表示选中的状态
   * */
  handleSwitchImg(that, funcName, chooseCode) {
    let { dispatch, startTask } = that.props
    const _index = that.props.imageCurrentIndex;
    const _len = that.props.num_results;
    let str = funcName === 'previousImg' ? '上': '下'
    if (funcName === 'previousImg' && !_index) {
      TopTips({theme: 'warning', message: '已经是第一张了！'})
      return;
    }
    if (funcName === 'nextImg' && (_index + 1) === _len) {
      TopTips({theme: 'warning', message: '已经是最后一张了！'})
      return;
    }

    if (chooseCode === null || chooseCode === undefined || chooseCode === '') {
      handleConfirm(that,funcName,'warning', '切换图片', `您还没有选择Code类型，是否确定切换${str}一张图片？`)
      return false
    }

    switchImg(that, funcName)
  },
  /**
   * @param that -- this
   * */
  rework(that) {
    let { dispatch, imageInfo } = that.props
    dispatch({
      type: `${MODEL}/r_t_s`,
      data: {
        rework: !imageInfo.rework ? '1': ''
      }
    })
  },
  turnOn(that) {
    let { dispatch, imageInfo} = that.props
    dispatch({
      type: `${MODEL}/r_t_s`,
      data: {
        turn_on: !imageInfo.turn_on ? '1': ''
      }
    })
  },
  /**
   * 检查是不是数组
   * @param obj
   * */
  isArray(obj) {
    return Object.prototype.toString.call(obj) === `[object Array]`
  },
  /**
   * 检查上传验证/复判文件的后缀是否正确
   * 多个后缀用 “,” 隔开
   * */
  checkUploadFileSuffix(fileName, suffix) {
    if (fileName.lastIndexOf('.') === -1) {
      return false
    }

    let regex;
    let _arr = suffix.split(',')
    if (_arr.length > 1) {
      regex = `(.${_arr.join('|.')})$`;
    } else {
      regex = `(.${suffix})$`
    }

    regex = new RegExp(regex, 'i');
    return regex.test(fileName)
  },
  /**
   * 检查上传验证/复判文件的大小
   * */
  checkUploadFileSize(file, size) {
    return file.size <= size
  },
  start_getImageLoad() {
    $(`#imageDetailLoad`).css('display', 'block')
  },
  end_getImageLoad() {
    $(`#imageDetailLoad`).css('display', 'none')
  },
  start_checkImageExist(that, url) {
    $(`#imageError`).css('display', 'none')
    $(`#image`).css('display', 'none')
    $(`#imageLoad`).show()
  },
  end_checkImageExist(that, url) {
    const img_url = that ? that.props.imageInfo.img_url : url;
    let img = new Image()

    img.src = testImageUrl + img_url
    img.onload = function() {
      $(`#image`).css('display', 'block')
      $(`#imageLoad`).hide()
    }
    img.onerror = function() {
      $(`#image`).css('display', 'none')
      $(`#imageError`).css('display', 'block')
      $(`#imageLoad`).hide()
    }
  },
  // setImageHandleAreaW(time) {
  //   time = time ? time : 0
  //   setTimeout(function() {
  //     let lw = $('.left-box')
  //     let rw = $('.right-box')
  //     let w = $(`#imageHandleArea`).width()
  //     lw.css('width', w - (500+40+80))
  //     // console.log('let w = $(`#imageHandleArea`).width()', w)
  //   }, time)
  // }
}

function handleConfirm(that,funcName, type, title, text) {

  let { dispatch, startTask } = that.props

  if (modalFlagIndex) return;
  modalFlagIndex++

  confirm({
    caption: title,
    text: text,
    type: type
  }).then(result => {

    modalFlagIndex = 0
    if(result === 'cancel'){
      // 点击取消
    } else {
      switchImg(that, funcName)
    }
  });
}
function switchImg(that, funcName) {

  if (modalFlagIndex) return;

  const { sysActive, rejudgeType, dispatch } = that.props

  let queryType;
  if (sysActive === 'rejudgeSys' && rejudgeType === 'product') {
    /** 复判，按产品复判 */
    queryType = 'rejudge_getImage'
  } else {
    queryType = 'handleQueryImage'
  }

  // 切换图片的时候禁止快捷键操作
  dispatch({
    type: `${MODEL}/startTask`,
    data: {
      startTask: false
    }
  })

  dispatch({
    type: `${MODEL}/` + funcName,
    callback: function(data){
      dispatch({
        type:`${MODEL}/${queryType}`,
        queryMap: null,
        callback:(res) =>{
          if (res.result) {
            dispatch({
              type: `${MODEL}/setImageInfo`,
              ...res
            })
            dispatch({
              type: `${MODEL}/changBaseState`,
              data: {
                direction_index: null
              }
            })
          }
        }
      })
      // console.log(data)
    }
  })
}


function directionKeyInital(that) {
  let {codeBtnActiveNumber, image_area_code_arr} = that.props
  // console.log(codeBtnActiveNumber, image_area_code_arr)
  // 如果没有valid_code，那么就返回 0，表示选中0
  // if (!codeBtnActiveNumber) return 0
  // 如果有valid_code那么就检查是否存在列表里
  let existOFarr = false
  for(let i = 0; i < image_area_code_arr.length; i++) {
    if (codeBtnActiveNumber === image_area_code_arr[i].code) {
      existOFarr = i
    }
  }
  //如果有valid_code，但是不存在列表里，那么就直接选中第一个，
  if (!existOFarr) return 0

  // 如果存在列表里，则返回列表里的下标，从下标处开始移动
  return existOFarr
}

async function moveCode(that, direction) {
  let _this = that;
  let {direction_index, dispatch, image_area_code_arr, codeBtnActiveNumber} = _this.props

  let _direction_index = direction_index
  if (_direction_index === null && !codeBtnActiveNumber) {
    _direction_index = 0

    let _code = image_area_code_arr[_direction_index].code;
    _this.props.dispatch({
      type: `${MODEL}/chooseCodeBtn`,
      data: {
        valid_code: _code,
        codeBtnActiveNumber: _code
      }
    })

    dispatch({
      type: `${MODEL}/changBaseState`,
      data: {
        direction_index: 0
      }
    })
    return

  }

  _direction_index = directionKeyInital(_this)

  // 左边是减1，如果该下标的依然存在，这切换到该下标下
  switch(direction) {
    case 'left':
      _direction_index = _direction_index - 1;
      break;
    case 'right':
      _direction_index = _direction_index + 1;
      break;
    case 'top':
      _direction_index = _direction_index - codeBtnRowNumber;
      break;
    case 'bottom':
      _direction_index = _direction_index + codeBtnRowNumber;
      break;
  }

  if (image_area_code_arr[_direction_index]) {
    let _code = image_area_code_arr[_direction_index].code;
    _this.props.dispatch({
      type: `${MODEL}/chooseCodeBtn`,
      data: {
        valid_code: _code,
        codeBtnActiveNumber: _code
      }
    })
    dispatch({
      type: `${MODEL}/changBaseState`,
      data: {
        direction_index: _direction_index
      }
    })
  }
}

function keyboardEvent(e,that) {
  e = e || window.event

  let {startTask, focusCodeInput,detailFullScrollState} = that.props

  if (!startTask ||  modalFlagIndex) return;

  // console.log('按下的按键', e.keyCode)
  let _this = that

  // 单键 不影响图片更新的快捷键
  switch(e.keyCode) {
    case 114: // f3 刷新页面
      e.preventDefault();
      window.location.reload();
      break;
    case 115: // f4 聚焦code文本框
      e.preventDefault();
      $('#searchOrAddCode').focus();
      break;
   /* 增加了收起之后，不需要铺满窗口了
    case 113:
      e.preventDefault();
      that.props.dispatch({
        type: `${MODEL}/changBaseState`,
        data: {
          detailFullScrollState: !detailFullScrollState
        },
        callback() {
          utils.setImageHandleAreaW()
        }
      })
    case 27:
      e.preventDefault();
      if(!detailFullScrollState) return
      that.props.dispatch({
        type: `${MODEL}/changBaseState`,
        data: {
          detailFullScrollState: false
        },
        callback() {
          utils.setImageHandleAreaW()
        }
      })*/
  }

  // 单键 影响图片更新的快捷键
  if(!focusCodeInput){
    switch(e.keyCode) {
      case 116: // f5 上一张
        e.preventDefault();
        utils.handleSwitchImg(_this, 'previousImg', _this.props.codeBtnActiveNumber);
        break;
      case 117: // f6 下一张
        e.preventDefault();
        utils.handleSwitchImg(_this, 'nextImg', _this.props.codeBtnActiveNumber);
        break;
      case 70: // F rework
        e.preventDefault();
        utils.rework(_this);
        break;
      case 71: // G turn on
        e.preventDefault();
        utils.turnOn(_this);
        break;
    }
  }

  if (!focusCodeInput) {
    // 选择code快捷键
    switch(e.keyCode) {
      case 37:
        e.preventDefault();
        moveCode(that, 'left')
        break
      case 38: // 上
        e.preventDefault();
        moveCode(that, 'top')
        break
      case 39: // 右
        e.preventDefault();
        moveCode(that, 'right')
        break
      case 40: // 下
        e.preventDefault();
        moveCode(that, 'bottom')
        break
      case 65:
        e.preventDefault();
        moveCode(that, 'left')
        break
      case 87: // 上
        e.preventDefault();
        moveCode(that, 'top')
        break
      case 68: // 右
        e.preventDefault();
        moveCode(that, 'right')
        break
      case 83: // 下
        e.preventDefault();
        moveCode(that, 'bottom')
        break
    }
  }


}




export default utils
