import _ from 'lodash/fp'

export const DEFAULT_VPORT = 3306

export const STATUS = {
  creating: 0, // 创建中
  doing: 1, // 流程处理中
  ok: 2, // 实例有效
  notInit: 3, // 尚未初始化
  rebooting: 4, // 实例重启中
  expired: -1, // 实例已隔离
  deleted: -2, // 实例已删除
  deleting: -3 // 实例删除中
}

export const STATUS_TITLE = {
  [STATUS.creating]: '创建中',
  [STATUS.doing]: '流程处理中',
  [STATUS.ok]: '实例有效',
  [STATUS.notInit]: '未初始化',
  [STATUS.rebooting]: '重启中',
  [STATUS.expired]: '实例已隔离',
  [STATUS.deleted]: '实例已删除',
  [STATUS.deleting]: '实例删除中'
}

export const STATUSES = _.keys(STATUS_TITLE).map(status => ({
  text: STATUS_TITLE[status],
  value: status
}))
