import { mockData, initState, mockImage } from '@models/rejudge3/state'
import { reducers } from '@models/rejudge3/reducers'
import effects  from '@models/rejudge3/effects'

export default {
  namespace:'rejudge3',
  state: initState,
  reducers: reducers,
  /**
   * action 包含了dispatch 传过来的参数
   * put 调用 reducers的函数
   * context react的对象
   * call ajax请求的方法
   * 内置了一个 reducers的 change 函数
   * */
  effects: effects
}
