import {TopTips} from '@tencent/bere'

export default {
  namespace: 'login',
  state: {
    user_name: '',
    pwd: '',
    isGroupLoading: false,
    loginData: [],
    testPwd: true,
    // testUsername: true,
    // 登录成功状态
    loginSuccess: false,
    // 登录失败
    // loginError: false
  },

  effects: {
    // 执行登录操作
    * fetchLogin(action, { select, put, call, cancel, context: { service } }) {
      yield put({
        type: 'change',
        payload: {
          isGroupLoading: true
        }
      });

      var { user_name, pwd, rememberPwd } = action.payload;
      // 是否永久存储
      var lifeShelf = '1d';
      // 是否保存密码
      if (!rememberPwd) lifeShelf = 0;
      var params = {
        username: user_name,
        password: pwd,
        expires: lifeShelf
      }
      const { resp } = yield call(service.postSignin, params)
      if (resp.code == "0") {
        yield put({
          type: 'change',
          payload: {
            isGroupLoading: false,
            // testUsername: true,
            testPwd: true,
            loginSuccess: true,
            user_name: user_name
          }
        })
        // 保存用户名
        localStorage.setItem("user_name", user_name);
        // 保存后台返回的 token
        localStorage.setItem("accessToken", resp.accessToken);
        localStorage.setItem("userId", resp.userId);
        localStorage.setItem("groupId", resp.groupId);
        // 保存后台返回的 账户类型
        //localStorage.setItem("user_type", resp.query.user_type);
        // 密码正确
        localStorage.setItem("remember", true);
        document.cookie = `op=${user_name}; expires=Tue, 19 Jan 2038 03:14:07 GMT;`;
        // 是否保存密码
        if (rememberPwd == true) {
          localStorage.setItem("pwd", pwd);
        } else {
          localStorage.removeItem("pwd");
        }
        if (action.callback) action.callback();
      }
      // else if(resp.code=='-6'){
      //   yield put({
      //     type: 'change',
      //     payload: {
      //       testUsername:false,
      //       isGroupLoading: false,
      //       testPwd: true,
      //     }
      //   })
      // } else if(resp.code=='-7'){
      //   yield put({
      //     type: 'change',
      //     payload: {
      //       testUsername: true,
      //       testPwd: false,
      //       isGroupLoading: false
      //     }
      //   })
      // }
      else {
        TopTips({ theme: 'error', message: '登录失败' });
        yield put({
          type: 'change',
          payload: {
            // testUsername: true,
            testPwd: false,
            isGroupLoading: false,
            // loginError: true,
          }
        })
      }

    }

  }
}
