export default {
  * change(action, {select, put, call, context}) {
    yield put({type: 'CHANGE_BASE_STATE', ...action.data})
    if(action.callback) {
      action.callback()
    }
  },
  /**
   * 请求四元组的下拉框数据
   * */
    * requestSite(action, {select, put, call, context}) {
    // const { imageCurrentIndex, num_results, sysActive } = yield select(state => state[MODEL])
    let params = {}
    params.timer = new Date().getTime()
    const res = yield call(context.service.site, params)
    let _resp = res.resp
    if (_resp.code === 0) {
      yield put({type: 'CHANGE_BASE_STATE', siteMap: res.resp.data})
    } else {
      TopTips({
        theme: 'error',
        message: _resp.msg,
        duration: TopTipsduration.err
      })
    }

  },
}

