function handleFlaskQ(queryMap) {
  let q_filters = []
  for (let k in queryMap) {
    // console.log('handleFlaskQ', queryMap[k], typeof queryMap[k])
    if (typeof queryMap[k] === 'object') {
      if (queryMap[k]) {
        q_filters.push({name: k, op: '>=', val: queryMap[k].from})
        q_filters.push({name: k, op: '<=', val: queryMap[k].to})
      }
    }
    else if (k === 'id') {
      q_filters.push({name: k, op: '>=', val: queryMap[k]})
    }
    else if (k === 'compare_rtir_code') {
      if (queryMap[k]) {
        q_filters.push({name: k, op: 'eq', val: 2})
      } else {
        q_filters.push({name: k, op: '<=', val: 2})
      }
    }
    else {
      if (queryMap[k] !== null && queryMap[k] !== '' && queryMap[k] !== undefined) {
        q_filters.push({name: k, op: 'eq', val: queryMap[k]})
      }
    }
  }
  return q_filters
}

