import fetch from 'isomorphic-fetch'
import {suggestServer} from '@config/urlMap'
import {notification} from '@tencent/bere'

function checkStatus(response) {
  if (response.status >= 200 && response.status < 300) {
    return response
  } else {
    var error = new Error(response.statusText)
    error.response = response
    throw error
  }
}

function parseJSON(response) {
  return response.json()
}
function download(url,filename) {
  return fetch(url+'&rnd='+Math.random(), {
    headers: {
      Authorization: `Bearer ${localStorage.getItem("accessToken")}`,// 统一header token，Access_Token_xxxxx 从localstroge里取
    }
  })
    .then(res => res.blob().then(blob => {
      if(!res.ok){
        notification.error("服务器错误")
        return
      }
      if(window.navigator.msSaveBlob){
        navigator.msSaveBlob(blob, filename);
        return
      }
      var a = document.createElement('a');
      var url = window.URL.createObjectURL(blob);
      a.href = url;
      a.download = filename;
      a.click();
      window.URL.revokeObjectURL(url);
    }));
}


export { download }
export default function (url, option) {
  let _option = {
    headers: {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
      'X-Requested-With': 'XMLHttpRequest',
      Authorization: `Bearer ${localStorage.getItem("accessToken")}`,// 统一header token，Access_Token_xxxxx 从localstroge里取
    },
  }

  if (option.headers) {
    option.headers = Object.assign(option.headers, _option.headers)
  } else {
    option.headers = _option.headers
  }
  if (option.body) {
    option.body = JSON.stringify(option.body)
  }
  option.credentials = "include" //带上cookie

  return fetch(`${suggestServer}${url}`, option)
    .then(checkStatus)
    .then(parseJSON)
}

