import React, {Component, PureComponent} from 'react'
import pathToRegexp from 'path-to-regexp'
import {Link} from 'react-router-dom'
import classNames from 'classnames'

import utils from '@modules/rejudge_v3/utils'

export const getMeunMatcheys = (flatMenuKeys, path) => {
  return flatMenuKeys.filter(item => {
    return pathToRegexp(item).test(path)
  })
}

export default class SiderMenu extends Component {
  constructor(props) {
    super(props)
    this.state = {
      openKeys: [],
    }
    this.handlerMenuToggle = this.handlerMenuToggle.bind(this)
    this.handlerSubMenuToggle = this.handlerSubMenuToggle.bind(this)
  }

  componentWillUnmount() { }

  // Get the currently selected menu
  getSelectedMenuKeys() {
    let { location: { pathname } } = this.props
    let pathArr = pathname.split('/')
    if( pathArr.length > 3 ) {
      pathname = pathArr.slice(0, 3).join('/')
    }
    return pathname
  }

  // slidebar 收缩
  handlerMenuToggle() {
    const { collapsed, onCollapse } = this.props
    onCollapse(!collapsed)
  }

  handlerSubMenuToggle(e, index) {

    let { openKeys } = this.state
    openKeys[index] = !openKeys[index]
    openKeys = Object.assign({}, openKeys)
    this.setState({
      openKeys
    })
    e.preventDefault();
  }

  render() {
    const { menuData, collapsed, location: { pathname } } = this.props
    const { openKeys } = this.state
    let selectedKeys = this.getSelectedMenuKeys()

    return (
      <div className='aside' data-focus='一级菜单'>
        <div className='menu'>
          <dl className='menu-list'>
            {menuData.map((item, index) => {
              return (
                item.children ?
                  <dd key={index}>
                    <Link to={item.path} title={item.name} className='menu-lv2'>
                      <span>{item.name}</span> <i className={classNames({
                        'white-down-icon': true
                      })} onClick={(e) => { this.handlerSubMenuToggle(e, index) }}></i>
                    </Link>
                    <ul role='menu' className='menu-sub' style={{ display: openKeys[index] ? 'none' : 'block' }}>
                      {item.children.map((child, idx) => {

                        return (
                          <li role='menuitem' key={idx}>
                            <Link
                              to={child.path}
                              title={child.name}
                              className={classNames({
                                'menu-lv3': true,
                                'act': child.path.indexOf(selectedKeys) > -1
                              })}>
                              <i className='ico-dot'></i> <span>{child.name}</span>
                            </Link>
                          </li>
                        )
                      })}
                    </ul>
                  </dd>
                  :
                  <dd role='menuitem' key={index}>
                    <Link
                      to={item.path}
                      title={item.name}
                      className='menu-lv2'
                      className={classNames({
                        'menu-lv2': true,
                        'act': selectedKeys.indexOf(item.path) > -1
                      })}>
                      <span>{item.name}</span>
                    </Link>
                  </dd>
              )
            })}
          </dl>
        </div>
        <a
          href='javascript:void(0);'
          title={collapsed ? '展开' : '收起'}
          className={classNames({
            'btn-fold-menu': true,
            'retract': collapsed
          })}
          onClick={this.handlerMenuToggle}>收起</a>
      </div>
    )
  }
}
