import { TopTips } from '@tencent/bere'
export default {
  namespace: 'account',
  state: {
    // 账户数据
    accountList: [],
    // 数据总数
    total: 0,
    // 每页数量
    limit: 10,
    // 当前页数
    page: 0,
    // 加载中
    isGroupLoading: false,
    // 是否显示增加账户列
    isAddAccount: false,
    // 编辑列
    isEditAccount: false,
    // 权限配置清单
    authorityList: [],
    // 是否显示权限配置清单
    isAuthorityList: false,
    // 编辑完成
    completedEdit: false,
    //分组列表
    groupList: [],
    //scopes描述列表
    scopesList: [],
    //当前用户信息
    currentAccountInfo: {}
  },
  effects: {
    * fetchCurrentAccount(action, { select, put, call, cancel, context: { service } }) {
      let id = localStorage.getItem('userId')
      const { resp } = yield call(service.getUserinfo, { id })
      console.log(resp);
      if (resp.code == '0') {
        yield put({
          type: 'change',
          payload: {
            currentAccountInfo: (resp.data[0]?resp.data[0]:resp.data)//可能是数组 简单处理
          }
        })
      }
    },
    // 获取账户列表
    * fetchAccount(action, { select, put, call, cancel, context: { service } }) {
      yield put({
        type: 'change',
        payload: {
          accountList: [],
          isGroupLoading: true
        }
      })
      var { page, limit } = action.payload;
      var params = {
        page: page,
        results_per_page: limit
      }
      const { resp } = yield call(service.getUserinfoAll, params)
      if (resp.code == '0') {
        yield put({
          type: 'change',
          payload: {
            accountList: resp.data,
            total: resp.num_results,
            isGroupLoading: false
          }
        })
      }
    },
    * fetchGroup(action, { select, put, call, cancel, context: { service } }) {
      const { resp } = yield call(service.getUserGroup, null)
      if (resp.code == '0') {
        yield put({
          type: 'change',
          payload: {
            groupList: resp.data,
          }
        })
      }
    },
    * fetchAuthScopes(action, { select, put, call, cancel, context: { service } }) {
      const { resp } = yield call(service.getAuthScopes, null)
      if (resp.code == '0') {
        yield put({
          type: 'change',
          payload: {
            scopesList: resp.data,
          }
        })
      }
    },
    // 增加账户
    * fetchAddAccount(action, { select, put, call, cancel, context: { service } }) {
      yield put({
        type: 'change',
        payload: {
          query: [],
          isGroupLoading: true
        }
      })
      const { resp } = yield call(service.postCreateUser, action.payload)
      if (resp.code == "0" || resp.code == 0) {
        //const { query } = resp.data
        yield put({
          type: 'change',
          payload: {
            // query: resp.query,
            isGroupLoading: false
          }
        })
        TopTips({
          theme: 'done',
          message: '新增成功'
        });

      } else if (resp.code == "403" || resp.code == 403) {
        TopTips({
          theme: 'warning',
          message: '该token无此权限'
        });
      } else if (resp.code == "1" || resp.code == -1) {
        TopTips({
          theme: 'warning',
          message: '用户名已存在'
        });
      } else {
        TopTips({
          theme: 'warning',
          message: resp.msg
        });
      }
      if (action.callback) action.callback();
    },

    // 编辑账户
    * fetchEditAccount(action, { select, put, call, cancel, context: { service } }) {
      yield put({
        type: 'change',
        payload: {
          query: [],
          isGroupLoading: true
        }
      })

      const { resp } = yield call(service.postUserinfoUpdate, action.payload, { level: 0 })
      if (resp.code == "0" || resp.code == 0) {
        yield put({
          type: 'change',
          payload: {
            // query: resp.query,
            isGroupLoading: false,
            completedEdit: true
          }
        })
        TopTips({
          theme: 'done',
          message: '编辑成功'
        });
        if (action.callback) action.callback();
      } else {
        TopTips({
          theme: 'warning',
          message: resp.msg
        });
      }
    },
    // 删除账户
    * fetchDelAccount(action, { select, put, call, cancel, context: { service } }) {
      yield put({
        type: 'change',
        payload: {
          isGroupLoading: true
        }
      })

      const { resp } = yield call(service.postUserinfoDelete, action.payload, { level: 0 })
      if (resp.code == "0" || resp.code == 0) {
        //const { query } = resp.data
        yield put({
          type: 'change',
          payload: {
            isGroupLoading: false
          }
        })
        TopTips({
          theme: 'done',
          message: '删除成功'
        });
        // 如果写有回调函数则执行
        if (action.callback) action.callback();
      } else {
        TopTips({
          theme: 'warning',
          message: resp.msg
        });
      }
    },
    // 权限配置清单
    * fetchAuthorityList(action, { select, put, call, cancel, context: { service } }) {
      yield put({
        type: 'change',
        payload: {
          authorityList: [],
          isGroupLoading: true
        }
      })
      const { resp } = yield call(service.userinfoPrivshow, null, { level: 0 })
      if (resp.code == "0" || resp.code == 0) {
        //const { query } = resp.data
        yield put({
          type: 'change',
          payload: {
            authorityList: resp.query,
            isGroupLoading: false
          }
        })
      }
    },

  }
}